goog.provide('H.service.metaInfo.IParser');



/**
 * This interface represents functionality to parse a tile based response of a remote object service
 * @interface
 */
H.service.metaInfo.IParser = function() {};


/**
 * To parse a response of a remote object service. If error occurres during parsing, method should
 * return {@code undefined}
 * @export
 * @param {H.service.metaInfo.ObjectProvider} provider The provider to create map objects for
 * @param {H.map.provider.SpatialTile} tile The according tile of the response
 * @param {*} response The response to parse
 * @return {H.service.metaInfo.IParser.Result|undefined}
 */
H.service.metaInfo.IParser.prototype.parse = function(provider, tile, response) {};


/**
 * To store the resulting map objects after parsing a remote object service response
 * @property {Array.<H.map.Spatial>=} spatials
 *    A list of parsed spatial objects or undefined if no spatials result
 * @property {Array.<H.map.Marker>=}
 *    markers A list of parsed marker objects or undefined if no markers result
 * @property {Array.<H.map.DomMarker>=} domMarkers
 *    A list of parsed DOM marker objects or undefined if no DOM markers result
 *
 * @typedef {{
 *   spatials: (Array.<H.map.Spatial>|undefined),
 *   markers: (Array.<H.map.Marker>|undefined),
 *   domMarkers: (Array.<H.map.DomMarker>|undefined)
 * }}
 */
H.service.metaInfo.IParser.Result;
