goog.provide('H.service.metaInfo.Spatial');



/**
 * This class represents a spatial object which stores and provides projected geometries on tile base.
 * @export
 * @extends {H.map.Spatial}
 * @implements {H.service.metaInfo.IObject}
 * @constructor
 * @param {H.service.metaInfo.ObjectProvider} provider The provider of this object.
 * @param {boolean} isClosed Indicates whether this spatial object represents a closed shape
 * @param {string|undefined} remoteId An ID to use for this object. If the ID evalues to a falsy the ID is ignored.
 * @param {H.map.Spatial.Options=} opt_options The options to apply
 */
H.service.metaInfo.Spatial = function(provider, isClosed, remoteId, opt_options) {
  'use strict';
  var options = opt_options || H.service.metaInfo.Spatial.spatialOptions_;

  // Initialize the map for the geometry data by tile key:
  this.geometryDataByTileKey_ = {};

  // Call the super constructor:
  options['provider'] = provider;
  H.map.Spatial.call(this, isClosed, options);

  if (remoteId) {
    this.remoteId_ = remoteId;
  }
};
H.lang.inherits(H.service.metaInfo.Spatial, H.map.Spatial);


/**
 * A reusable object used as H.map.Spatial.Options to specify the spatial's provider
 * @private
 * @type {H.map.Spatial.Options}
 */
H.service.metaInfo.Spatial.spatialOptions_ = /** @type {H.map.Spatial.Options}*/ ({});


//###############################################  IObject  ############################################################
/**
 * The unique ID of this object defined by the remote service
 * @type {string}
 */
H.service.metaInfo.Spatial.prototype.remoteId_;


/**
 * @override
 * @export
 */
H.service.metaInfo.Spatial.prototype.getRemoteId = function() {
  return this.remoteId_;
};


/**
 * The reference counter for this object
 * @private
 * @type {number}
 */
H.service.metaInfo.Spatial.prototype.referenceCounter_ = 0;


/**
 * @override
 * @export
 */
H.service.metaInfo.Spatial.prototype.retain = function() {
  'use strict';
  if (this.remoteId_) {
    ++this.referenceCounter_;
  }
};


/**
 * @override
 * @export
 */
H.service.metaInfo.Spatial.prototype.release = function() {
  'use strict';
  if (this.remoteId_) {
    // All references released?
    if (!--this.referenceCounter_) {
      // We have to release the remote object from its provider:
      this.getProvider().removeObject(this);
    }
  }
  return this.referenceCounter_ < 1;
};



//############################################  GEOMETRY DATA  #########################################################
/**
 * To store the geometry and the hybrid index of a spatial object for a certain tile
 * @private
 * @constructor
 * @param {Array.<Array.<H.math.Point>>} geometry
 * @param {number=} opt_hybridIdx
 */
H.service.metaInfo.Spatial.GeometryData_ = function(geometry, opt_hybridIdx) {
  'use strict';

  /**
   * @type {Array.<Array.<H.math.Point>>}
   */
  this.geometry = geometry;

  if (opt_hybridIdx >= 0) {
    this.hybridIdx = opt_hybridIdx;
  }
};


/**
 * @type {number}
 */
H.service.metaInfo.Spatial.GeometryData_.prototype.hybridIdx = -1;


/**
 * A reusable GeometryData_ instance to use if no geometry data could be found
 * @private
 * @type {H.service.metaInfo.Spatial.GeometryData_}
 */
H.service.metaInfo.Spatial.emptyGeometryData_ = new H.service.metaInfo.Spatial.GeometryData_([]);


/**
 * A mapping of tile keys and geometry data for the tile
 * @private
 * @type {Object.<string, H.service.metaInfo.Spatial.GeometryData_>}
 */
H.service.metaInfo.Spatial.prototype.geometryDataByTileKey_;


/**
 * A internal helper function to fetch geometry data for a given tile
 * @private
 * @param {H.map.provider.SpatialTile} tile The tile for which the geometry data are requested
 * @return {H.service.metaInfo.Spatial.GeometryData_}
 */
H.service.metaInfo.Spatial.prototype.getGeometryData_ = function(tile) {
  return this.geometryDataByTileKey_[tile['key']] || H.service.metaInfo.Spatial.emptyGeometryData_;
};


//##############################################  GEOMETRY  ############################################################
/**
 * @override
 * @export
 */
H.service.metaInfo.Spatial.prototype.getGeometry = function(tile) {
  'use strict';
  return this.getGeometryData_(tile).geometry;
};


/**
 * @override
 * @export
 */
H.service.metaInfo.Spatial.prototype.getHybridIdx = function(tile) {
  'use strict';
  return this.getGeometryData_(tile).hybridIdx;
};


/**
 * Sets the geometry for a tile. Be aware that NO invalidation is triggered, because we expect that geometries are only
 * set during RemoteTileProvider#createTileInternal which triggers an update by itself!
 * @param {H.map.provider.SpatialTile} tile The tile for which the geometry is added
 * @param {Array.<Array.<H.math.Point>>} geometry The geometry to add
 * @param {number=} opt_hybridIdx The index where the outline geometry is starting.
 *        If ommited the geometry is treated as none-hybrid
 */
H.service.metaInfo.Spatial.prototype.setGeometry = function(tile, geometry, opt_hybridIdx) {
  // TODO Should we throw an exception if hybridIdx is set but this.isClosed() returns false?
  this.geometryDataByTileKey_[tile['key']] = new H.service.metaInfo.Spatial.GeometryData_(geometry, opt_hybridIdx);
};


/**
 * Removes the geometry for a the given tile. Be aware that NO invalidation is triggered, beacuse we expect that
 * geometries are only removed during a Cache#onDrop!
 * @param {H.map.provider.SpatialTile} tile The tile for which the geometry is removed
 */
H.service.metaInfo.Spatial.prototype.removeGeometry = function(tile) {
  delete this.geometryDataByTileKey_[tile['key']];
};
