goog.provide('H.service.JsonX');



/**
 * Sends a XHR request and invokes the provided callback once the request finished.
 * Must not be published, platform-API internal use only!
 * @export
 * @constructor
 * @param {!(string|H.service.Url)} url The URL of the JSON-XHR service to be called
 * @param {function(?, (string|undefined))} callback The function to be called once the request finished.
 *        It gets the JSON parse result and an optional error type as arguments. Possible error types are:
 *        <ul>
 *          <li>{@code undefined} - if no error occurred </li>
 *          <li>{@code "abort"} - if the request was explicit aborted {@see H.service.JsonX.prototype.abort}}</li>
 *          <li>{@code "timeout"} - if the request timed out {@see H.net.Xhr.Options#timeout} </li>
 *          <li>{@code "network"} - if a {@code NetworkError} occurred </li>
 *          <li>{@code "parse"} - if the response has no valid JSON format</li>
 *        </ul>
 *
 * @param {H.net.Xhr.Options=} opt_options The optional parameters for this underlaying XHR request
 */
H.service.JsonX = function(url, callback, opt_options) {
  'use strict';
  var options = opt_options || {},
      headers;

  if (typeof callback !== 'function') {
    throw new TypeError('Invalid callback');
  }

  if (typeof(url) === 'string') {
    url = /** @type {!H.service.Url} */ (H.service.Url.parse(url, options['baseURL']));
  } else if (!(url instanceof H.service.Url)) {
    throw new TypeError('Invalid url');
  }

  //setup application/json as defautl value for accept header
  options['headers'] = headers = options['headers'] || {};
  if (!headers['accept']) {
    headers['accept'] = 'application/json';
  }


  /**
   * @private
   * @type {function(?, (string|undefined))}
   */
  this.callback_ = callback;

  /**
   * @private
   * @type {H.net.Xhr}
   */
  this.request_ = new H.net.Xhr(url.toString(), this.xhrCallback_.bind(this), options);
};


/**
 * The callback for the XHR request
 * @private
 * @param {string=} opt_responseText
 * @param {string=} opt_errorType
 */
H.service.JsonX.prototype.xhrCallback_ = function(opt_responseText, opt_errorType) {
  'use strict';
  var callback = this.callback_,
      parsedJSON;
  if (!opt_errorType) {
    try {
      parsedJSON = JSON.parse(/** @type {string} */(opt_responseText));
    } catch (e) {
      opt_errorType = 'parse';
    }
  }
  delete this.callback_;
  delete this.request_;
  callback(parsedJSON, opt_errorType);
};


/**
 * It aborts an ongoing request.
 * If the request is not already finished, the request's callback is invoked with error "abort"
 * @export
 */
H.service.JsonX.prototype.abort = function() {
  'use strict';
  // If the request was not finished yet?
  if (this.request_) {
    this.request_.abort();
  }
};


/**
 * @property {number=} timeout
 *           The number of milliseconds to wait before the request timed out (0 means no timeout)
 * @property {boolean=} withCredentials
 *           Indicates whether user credentials should be included with the request, defaults to {@code false}
 *           This option is ignored by IE9 because of technical limitations.
 * @property {boolean=} cors
 *           Indicates a Cross-Origin Resource Sharing request, defaults to {@code false}
 *           This flag is only necessary for IE9, where a XDomainRequest is API-internally used.
 * @property {string=} method
 *           The http request method which should be used. (i.e GET, POST...)
 * @property {Object=} data
 *           The data which will be send by the request
 *
 *           Map of request headers and their values which should
 *           be set for the current request
 * @property {string=} baseURL base url which will be used for
 *           provided url paths
 *
 * @typedef {{
 *   timeout:         (number|undefined),
 *   withCredentials: (boolean|undefined),
 *   cors:            (boolean|undefined),
 *   method:          (string|undefined),
 *   data:            (string|undefined),
 *   headers:         (Object.<string,string>|undefined),
 *   baseURL:         (string|undefined)
 * }}
 */
H.service.JsonX.Options;

