goog.provide('H.service.GeocodingService');


goog.require('H.service.AbstractRestService');
goog.require('H.service.Platform');



/**
 * This class encapsulates the Geocoding REST API in a service stub with calls
 * to its various resources implemented.
 *
 * An instance of this class can be retrieved by calling the factory method on
 * a platform instance. {@link H.service.Platform#createGeocodingService}.
 *
 * @example
 * var platform = new H.service.Platform(),
 *     geocoder = platform.getGeocodingService();
 *
 * var geocodingParams = {
 *       searchText: '425 W Randolph Street, Chicago'
 *     },
 *     onResult = function(result) {
 *       console.log(result.view[0].result[0].displayPosition);
 *     },
 *     onError = function(error) {
 *        console.log(error);
 *     };
 * geocoder.geocode(geocodingParams, onResult, onError);
 *
 * @constructor
 * @extends {H.service.AbstractRestService}
 * @param {H.service.GeocodingService.Options=} opt_options
 * @publish
 * @export
 */
H.service.GeocodingService = function(opt_options) {
  'use strict';
  var options = H.service.mergeOptions(H.service.getOptions('geocoding'), opt_options);
  options['callbackKey'] = 'jsoncallback';

  H.util.getSuperProto(H.service.GeocodingService).constructor.call(this, options);

  this.reverseSubDomain_ = options['reverseSubDomain'];
};
H.lang.inherits(H.service.GeocodingService, H.service.AbstractRestService);


/**
 * This method sends a reverse geocoding request to Geocoder REST API and
 * calls the {@code onResult} callback function once the service response
 * was received - providing a {@link H.service.ServiceResult} object -
 * or the {@code onError} callback if a communication error occured.
 *
 * Please refer to the
 * [Geocoder REST API documentation](http://developer.here.com/rest-apis/documentation/geocoder)
 * for information on available parameters and the response object structure.
 *
 * @method
 * @param {H.service.ServiceParameters} geoodingParams the service
 *  parameters to be sent with the geocoding request.
 * @param {function(H.service.ServiceResult)} onResult this function
 *  will be called once the Geocoder REST API provides a response to the
 *  request.
 * @param {function(Error)} onError  this function will
 *  be called if a communication error occurs during the JSON-P request
 * @return {H.service.JsonpRequestHandle}
 *
 * @publish
 * @export
 */
H.service.GeocodingService.prototype.geocode = function(geoodingParams, onResult, onError) {
  'use strict';
  var url = this.getUrl()
      .clone()
      .addSubPath('geocode.json')
      .mergeQuery(geoodingParams);
  return this.restApiRequest(url, onResult, onError);
};


/**
 * This method sends a reverse geocoding request to Geocoder REST API and
 * calls the {@code onResult} callback function once the service response
 * was received - providing a {@link H.service.ServiceResult} object -
 * or the {@code onError} callback if a communication error occured.
 *
 * Please refer to the
 * [Geocoder REST API documentation](http://developer.here.com/rest-apis/documentation/geocoder)
 * for information on available parameters and the response object structure.
 *

 * @param {H.service.ServiceParameters} reverseGeocodingParams the
 *  service parameters to be sent with the reverse geocoding request
 * @param {function(H.service.ServiceResult)} onResult this function
 *  will be called once the Geocoder REST API provides a response to the
 *  request.
 * @param {function(Error)} onError this function will
 *  be called if a communication error occurs during the JSON-P request
 * @return {H.service.JsonpRequestHandle}
 *
 * @publish
 * @export
 */
H.service.GeocodingService.prototype.reverseGeocode = function(reverseGeocodingParams, onResult, onError) {
  'use strict';
  var url = this.getUrl()
      .clone()
      .addSubPath('reversegeocode.json')
      .mergeQuery(reverseGeocodingParams);

  this.reverseSubDomain_ && url.addSubDomain(this.reverseSubDomain_);

  return this.restApiRequest(url, onResult, onError);
};


/**
 * This method sends a landmark search request to Geocoder REST API and
 * calls the {@code onResult} callback function once the service response
 * was received - providing a {@link H.service.ServiceResult} object -
 * or the {@code onError} callback if a communication error occured.
 *
 * Please refer to the
 * [Geocoder REST API documentation](http://developer.here.com/rest-apis/documentation/geocoder)
 * for information on available parameters and the response object structure.
 *
 * @param {H.service.ServiceParameters} searchParams the
 *  service parameters to be sent with the reverse geocoding request
 * @param {function(H.service.ServiceResult)} onResult this function
 *  will be called once the Geocoder REST API provides a response to the
 *  request.
 * @param {function(Error)} onError this function will
 *  be called if a communication error occurs during the JSON-P request
 * @return {H.service.JsonpRequestHandle}
 *
 * @publish
 * @export
 */
H.service.GeocodingService.prototype.search = function(searchParams, onResult, onError) {
  'use strict';
  var url = this.getUrl()
      .clone()
      .addSubPath('search.json')
      .mergeQuery(searchParams);
  return this.restApiRequest(url, onResult, onError);
};


/**
 * @typedef {{
 *   subDomain: (string|undefined),
 *   path: (string|undefined)
 * }}
 * @property {string=} subDomain the sub-domain of the geo-coding service relative
 *  to the platform's base URL, default is {@code 'geocoder'}
 * @property {string=} path the path of the map tile service, default is
 *  {@code '6.2'}
 * @publish
 */
H.service.GeocodingService.Options;


//Storing default options
H.service.setOptions('geocoding', {
  'subDomain': 'geocoder',
  'path': '6.2',
  'reverseSubDomain': 'reverse'
});


/**
 * This method returns an implementation instance of
 * {@link H.service.GeocodingService}
 * @param {H.service.GeocodingService.Options=} opt_options an optional set of options for the new
 *  geocoding service to connect to
 * @return {H.service.GeocodingService} a new geocoding service instance
 * @export
 * @publish
 */
H.service.Platform.prototype.getGeocodingService = function(opt_options) {
  'use strict';
  return /** @type {H.service.GeocodingService} */ (this.configure(
      new H.service.GeocodingService(opt_options)));
};



