goog.provide('H.service.metaInfo.ObjectProvider');


goog.require('H.service.metaInfo.IObject');



/**
 * This class represents a provider for remote objects which requests the data from a service and converts them to
 * map objects.
 * @export
 * @extends {H.map.provider.ObjectProvider}
 * @constructor
 * @param {H.service.metaInfo.IParser} parser The parser to use for parsing the responses
 * @param {H.service.metaInfo.ObjectProvider.Options=} opt_options The options for the provider
 *
 */
H.service.metaInfo.ObjectProvider = function(parser, opt_options) {
  'use strict';

  /**
   * @private
   * @type {H.service.metaInfo.IParser}
   */
  this.parser_ = parser;

  /**
   * @private
   * @type {H.service.metaInfo.ObjectProvider.Options}
   */
  this.options_ = /** @type {H.service.metaInfo.ObjectProvider.Options} */ ({});
  H.lang.extend(this.options_, opt_options || this.options_);

  H.map.provider.ObjectProvider.call(this, opt_options);

  this.remoteObjects_ = {};

};
H.lang.inherits(H.service.metaInfo.ObjectProvider, H.map.provider.ObjectProvider);


/**
 * @override
 */
H.service.metaInfo.ObjectProvider.prototype.dispose = function() {
  'use strict';
  H.map.provider.ObjectProvider.prototype.dispose.call(this);
  // TODO
  // Dispose related instance properties!
};


//################################################  REQUEST OBJECTS  ###################################################
/**
 * @private
 * @type {Array}
 */
H.service.metaInfo.ObjectProvider.emptyArray_ = [];


/**
 * @export
 * @override
 */
H.service.metaInfo.ObjectProvider.prototype.requestMarkers = function(rect, zoom, visiblesOnly, cacheOnly) {
  'use strict';
  return H.service.metaInfo.ObjectProvider.emptyArray_;
};


/**
 * @export
 * @override
 */
H.service.metaInfo.ObjectProvider.prototype.requestDomMarkers = function(rect, zoom, visiblesOnly, cacheOnly) {
  'use strict';
  return H.service.metaInfo.ObjectProvider.emptyArray_;
};


/**
 * @export
 * @override
 */
H.service.metaInfo.ObjectProvider.prototype.providesSpatials = function() {
  'use strict';
  return true;
};


/**
 * @export
 * @override
 */
H.service.metaInfo.ObjectProvider.prototype.requestSpatials = function(rect, zoom, visiblesOnly, cacheOnly) {
  'use strict';
  return H.service.metaInfo.ObjectProvider.emptyArray_;
};


/**
 * @export
 * @override
 */
H.service.metaInfo.ObjectProvider.prototype.requestSpatialsByTile = function(tile, visiblesOnly, cacheOnly) {
  'use strict';
  var spatials = tile.$$spatials$$,
      response,
      parserResult;
  if (!spatials && (response = tile.$$response$$)) {
    delete tile.$$response$$;
    parserResult = this.parser_.parse(this, tile, response);
    if (parserResult) {
      spatials =
          tile.$$spatials$$ =
              parserResult['spatials'];
    }
  }
  return spatials || H.service.metaInfo.ObjectProvider.emptyArray_;
};


/**
 * To register a response object for a given tile
 * @param {H.map.provider.SpatialTile} tile The tile for which the response should be registered
 * @param {Object.<string,*>|undefined} response The response to register
 */
H.service.metaInfo.ObjectProvider.prototype.registerResponse = function(tile, response) {
  'use strict';
  delete tile.$$spatials$$;
  tile.$$response$$ = response;
};


//##############################################  REMOTE OBJECTS  ######################################################
/**
 * To map IDs of remote objects to map objects.
 * @private
 * @type {Object.<string, H.service.metaInfo.Spatial>}
 */
H.service.metaInfo.ObjectProvider.prototype.remoteObjects_;


/**
 * To fetch an managed remote object.
 * The ID must not equal any property name of the Javascript object's prototype!
 * @param {string} id The remote ID of the object to fetch
 * @return {H.map.Object|undefined} the fetched object or undefined if it is not managed
 */
H.service.metaInfo.ObjectProvider.prototype.fetchObjectById = function(id) {
  'use strict';
  return this.remoteObjects_[id];
};


/**
 * To add an objects to the remote object managment.
 * @throws Error if the remote object was already added before
 * @param {H.service.metaInfo.Spatial} object The object to add
 */
H.service.metaInfo.ObjectProvider.prototype.addObject = function(object) {
  'use strict';
  var id = object.getRemoteId();
  if (this.remoteObjects_[id]) {
    throw 'Remote object ' + id + ' allready added';
  }
  this.remoteObjects_[id] = object;
};


/**
 * To remove an object from the remote object managment.
 * This method must not called before all references to this remote object are released!
 * Otherwise all state (like style, visibility etc.) will be lost
 * The ID must not equal any property name of the Javascript object's prototype!
 * @param {H.service.metaInfo.IObject} object The object to remove
 */
H.service.metaInfo.ObjectProvider.prototype.removeObject = function(object) {
  'use strict';
  delete this.remoteObjects_[object.getRemoteId()];
};


//#################################################  OPTIONS  ##########################################################
/**
 * Options which are used to initialize the meta-info povider
 * @property {number=} min
 *        The minimal supported zoom level, default is {@code 0}
 * @property {number=} max
 *        The max maximal supported zoom level, default is {@code 22}
 * @typedef {{
 *   min: (number|undefined),
 *   max: (number|undefined)
 * }}
 */
H.service.metaInfo.ObjectProvider.Options;
