goog.provide('H.service.metaInfo.TileProvider');


goog.require('H.service.metaInfo.ObjectProvider');
goog.require('H.service.metaInfo.Parser');
goog.require('H.service.metaInfo.Service');



/**
 * This class represents
 * [Metainfo Tiles](http://developer.here.com/rest-apis/documentation/enterprise-map-tile/topics/tile-metainfo.html)
 * that annotate map tiles {@link H.service.MapTileProvider}
 * with additional data that describes objects (buildings, labels, public transport etc.) rendered on
 * map tiles.
 *
 * @publish
 * @extends {H.map.provider.RemoteTileProvider}
 * @constructor
 * @export
 *
 * @param {H.service.metaInfo.Service} service the meta tile service which holds information from
 *   about the soutce of the tiles
 * @param {H.service.ServiceParameters=} opt_params an additional set of URL parameters
 * @param {H.service.metaInfo.TileProvider.Options=} opt_options additional parameters
 */
H.service.metaInfo.TileProvider = function(service, opt_params, opt_options) {
  'use strict';

  var options = opt_options || {},
      params = opt_params || {};

  /**
   * @private
   * @type {H.service.ServiceParameters}
   */
  this.params_ = params;

  if (!service || !(service instanceof H.service.metaInfo.Service)) {
    throw new Error('InvalidArgument: service required');
  }

  /**
   * Meta info tile service.
   * @private
   * @type {H.service.metaInfo.Service}
   */
  this.service_ = service;

  this.updateAndReload_ = H.lang.bind(this.updateAndReload_, this);
  this.service_.addEventListener('versionupdate', this.updateAndReload_);

  /**
   * @private
   * @type {H.service.Url}
   */
  this.baseUrl_ = service.getUrl()
      .clone().addSubPath('maptile/' + service.getVersion() + '/normal.day')
      .mergeQuery(this.additionalParams_)
      .mergeQuery(params);

  /**
   * @private
   * @type {Array.<string>|null}
   */
  this.shards_ = service.getShards() || null;

  /**
   * @type {number}
   */
  this['tileSize'] = options['tileSize'] || 256;

  /**
   * @private
   * @type {number}
   */
  this.pixelratio_ = options['pixelratio'] || 1;

  /**
   * @private
   * @type {number}
   */
  this.tileCacheSize_ = options['tileCacheSize'] || 256;

  /**
   * Object provider stores geometries and is responsible for geometry management.
   * @private
   * @type {H.service.metaInfo.ObjectProvider}
   */
  this['objectProvider_'] = new H.service.metaInfo.ObjectProvider(
      new H.service.metaInfo.Parser(options['categoryFilter']));
  this['objectProvider_'].setParentEventTarget(this);
  this.onObjectProviderUpdate_ = H.lang.bind(this.onObjectProviderUpdate_, this);
  this['objectProvider_'].addEventListener('update', this.onObjectProviderUpdate_);

  /**
   * Simple canvas pool resembles one in SpatialTileProvider.
   * TODO: we need proper API-wide canvas pool
   * @private
   * @type {Array.<HTMLCanvasElement>}
   */
  this.canvasPool_ = [];

  this.onTileDropped_ = H.lang.bind(this.onTileDropped_, this);

  /**
   * Tile cache.
   * @type {H.util.Cache}
   * @private
   */
  this.cache_ = this['getCache']();

  H.map.provider.RemoteTileProvider.call(this, {'min': 0, 'max': 20});
};
H.lang.inherits(H.service.metaInfo.TileProvider, H.map.provider.RemoteTileProvider);


/**
 * @override
 */
H.service.metaInfo.TileProvider.prototype.getCache = function() {
  'use strict';
  return this.cache_ || new H.util.Cache(this.tileCacheSize_, this.onTileDropped_);
};


/**
 * Method revokes canvas to canvas pool when tile drops from cache.
 * @private
 * @param {string} tileKey The ID of the dropped tile
 * @param {H.map.provider.SpatialTile} tile The dropped tile
 * @param {number} size The size of the entry
 */
H.service.metaInfo.TileProvider.prototype.onTileDropped_ = function(tileKey, tile, size) {
  'use strict';
  // TODO We should release the objects and data not before the tile dropped out of a second cache where the tiles
  // are stored without their canvas
  var objectProvider,
      objects,
      i,
      object;
  // TODO Instead of doing the object releasing in the TileProvider we should do it in the SpatialTile.
  // retain() and release() should be moved then in to map.Object
  objectProvider = this['objectProvider_'],
  objects = tile.getObjects(),
  i = objects.length;
  while (i--) {
    object = objects[i];
    object.removeGeometry(tile);
    object.release();
  }
  if (tile['data']) {
    this.canvasPool_.push(tile.releaseCanvas());
    tile.releaseData();
  }
};


/**
 * Method fetches canvas from canvas pool or creates new canvas.
 * @private
 * @return {HTMLCanvasElement}
 */
H.service.metaInfo.TileProvider.prototype.getCanvas_ = function() {
  'use strict';
  return this.canvasPool_.pop() || H.service.util.global.document.createElement('canvas');
};


/**
 * Method redispatches update event from object provider.
 * @private
 */
H.service.metaInfo.TileProvider.prototype.onObjectProviderUpdate_ = function() {
  'use strict';
  this.dispatchEvent('update');
};


/**
 * @protected
 * @export
 * @override
 */
H.service.metaInfo.TileProvider.prototype.requestInternal = function(x, y, z, onSuccess, onError) {
  'use strict';
  var tileUrl = this.baseUrl_.clone(),
      shards = this.shards_,
      tileSize = this['tileSize'],
      tileKey = this['getTileKey'](x, y, z),
      cache = this.cache_,
      // TODO: here we hit cache for the second time, just to fetch tile for checking
      // if canvas is valid or not, that's wrong it should be done once in RTP, must be fixed with
      // aligning spatial tile and tile
      tile = cache.get(tileKey),
      cancelable,
      xhr,
      errorOccurred;

  // TODO: isCanvasValid - wrong too narrow, too specific for spatial tile see comment in spatial tile
  if (tile && !tile.isCanvasValid()) {
    // TODO: here's a bit of a trick: if tile's canvas is invalid we hand back canvas element and not
    // a data, createTileInternal is aware of this distinction and will simply rerender this canvas
    onSuccess(this.getCanvas_(), null);
    cancelable = {
      'cancel': function() {}
    };
  } else {

    if (shards) {
      tileUrl.addSubDomain(shards[(z + y + x) % shards.length]);
    }

    tileUrl.addSubPath(z + '/' + x + '/' + y + '/' + tileSize + '/png8');

    // throttle meta info to 500 ms - it's additional data after all.
    var timeoutId = H.service.util.global.setTimeout(function() {
      xhr = new H.net.Xhr(tileUrl.toString(), function(responseText, errorMsg) {
        if (errorMsg) {
          onError();
        } else {
          onSuccess(null, responseText);
        }
      });
    }, 500);
  }

  return /** @type {H.util.ICancelable}*/ (cancelable || {
    'cancel': function() {
      if (xhr) {
        // TODO: chrome hangs socket when too many aborts happen
        // xhr.abort();
      } else {
        H.service.util.global.clearTimeout(timeoutId);
      }
    }
  });
};


/**
 * @override
 * @export
 * @protected
 */
H.service.metaInfo.TileProvider.prototype.createTileInternal = function(x, y, z, data, opt_options) {
  'use strict';

  var objectProvider = this['objectProvider_'],
      canvas,
      tile,
      objects,
      i;

  canvas = /** @type {HTMLCanvasElement} */ (data) || this.getCanvas_();

  if (data) {
    // TODO: ok, shoot me, we hit cache again, if we get canvas element we just rerender tile.
    // for all the love in the world, we must align two level caching between tile & spatial tile
    tile = /** @type {H.map.provider.SpatialTile} */ (this.cache_.get(this['getTileKey'](x, y, z)));
    tile.render(/** @type {HTMLCanvasElement} */ (data));
  } else {
    // we got actual data, create a new tile and render it
    tile = new H.map.provider.SpatialTile(
        this.getTileKey(x, y, z), x, y, z, this['tileSize'], this.pixelratio_, objectProvider);
    objectProvider.registerResponse(tile, opt_options);

    if (!tile.render(canvas)) {
      this.canvasPool_.push(canvas);
    } else {
      // TODO Instead of doing the object retaining in the TileProvider we should do it in the SpatialTile.
      // retain() and release() should be moved then in to map.Object
      objects = tile.getObjects(),
      i = objects.length;
      while (i--) {
        objects[i].retain();
      }
    }
  }

  return tile;
};


/**
 * Disposes of this map tile provider instance.
 */
H.service.metaInfo.TileProvider.prototype.dispose = function() {
  'use strict';
  this['objectProvider_'].dispose();
  this.service_.removeEventListener('versionupdate', this.updateAndReload_);
  H.map.provider.RemoteTileProvider.prototype.dispose.call(this);
};


/**
 * Callback that's invoked when version information arrives from the server.
 * @private
 */
H.service.metaInfo.TileProvider.prototype.updateAndReload_ = function() {
  'use strict';
  var service = this.service_;
  this.baseUrl_ = service.getUrl()
      .clone()
      .addSubPath('maptile/' + service.getVersion() + '/normal.day')
      .mergeQuery(this.additionalParams_)
      .mergeQuery(this.params_);

  this.reload(false);
};


/**
 * Additional GET parameters needed to fetch meta info.
 * @private
 */
H.service.metaInfo.TileProvider.prototype.additionalParams_ = {
  'metadata': 'metaonly',
  'mgen': '2'
};


/**
 * Configuration object which can be used to initialize the TileProvider.
 * @publish
 * @property {number=} tileCacheSize
 *    The number of fully rendered spatial tiles that are cached for immediate reuse, default is {@code 32}
 * @property {number=} tileSize
 *    The size of the tiles rendered by this provider (must be power of 2, default is {@code 256})
 * @property {number=} pixelRatio
 *    The pixelRatio to use for over-sampling in cases of high-resolution displays
 * @property (Array.<string>=) A list of meta-info category names which should be suppressed. See
 *      [Metainfo Tile](http://developer.here.com/rest-apis/documentation/enterprise-map-tile/topics/tile-metainfo.html)
 *      for valid category names.
 *
 * @typedef {{
 *   tileCacheSize: (number|undefined),
 *   pixelratio: (number|undefined),
 *   tileSize: (number|undefined),
 *   categoryFilter: (Array.<string>|undefined)
 * }}
 */
H.service.metaInfo.TileProvider.Options;
