goog.provide('H.service.DefaultLayers');


goog.require('H.service.MapTileService');
goog.require('H.service.Platform');
goog.require('H.service.TrafficIncidentsProvider');
goog.require('H.service.TrafficIncidentsService');


/**
 * This method creates a pre-configured set of HERE tile layers for convenient use with the
 * map.
 *
 * This method returns an object holding the three default map types
 *
 * - {@code normal}
 * - {@code satellite}
 * - {@code terrain}
 *
 * Each of the map types in turn contains the following variant tile layers:
 *
 * - {@code map}
 * - {@code xbase}
 * - {@code base}
 * - {@code traffic}
 * - {@code panorama}
 * - {@code labels}
 *
 * In addition the {@code normal} map type contains the {@code transit} and {@code metaInfo} variant.
 * Collection of default map types also includes {@code incidents} type, that provides an overlay that
 * displays current trffic incidents.
 *
 * @example
 * // Create the default layers
 * var layers = platform.createDefaultLayers();
 *
 * // Set the satellite map type's traffic variant as the map's base layer
 * map.setBaseLayer(layers.satellite.traffic);
 *
 * @param {number=} opt_tileSize optional tile size to be queried from the HERE Map Tile API, default is {@code 256}
 * @param {number=} opt_ppi optional 'ppi' parameter to use when querying tiles, default is not specified
 * @param {string=} opt_lg optional primary language parameter, default is not specified
 * @param {string=} opt_lg2 optional secondary language parameter, default is not specified
 * @param {string=} opt_style optional 'style' parameter to use when querying map tiles, default is not specified
 * @param {boolean=} opt_pois indicates if pois are displayed on the map
 *
 * @return {Object.<string, H.service.MapType>} a set of tile layers ready to use
 *
 * @export
 * @publish
 */
H.service.Platform.prototype.createDefaultLayers = function(opt_tileSize, opt_ppi, 
        opt_lg, opt_lg2, opt_style, opt_pois) {
  'use strict';
  var baseMT = this.getMapTileService(),
      aerialMT = this.getMapTileService({type: 'aerial'}),
      trafficMT = this.getMapTileService({type: 'traffic'}),
      panoMT = this.getMapTileService({type: 'pano'}),
      metaInfoService = this.getMetaInfoTileService(),
      incidents = this.getTrafficIncidentsService(),
      incidentsProvider,
      tileServices = [baseMT, aerialMT],
      params = /** @type {H.service.ServiceParameters}*/ ({}),
      groupsDict = {},
      currentGroup,
      tileSize = opt_tileSize || 256;

  if (opt_ppi === 320) {
    params['ppi'] = opt_ppi;
  }
  if (opt_lg) {
    params['lg'] = opt_lg;
  }
  if (opt_lg2) {
    params['lg2'] = opt_lg2;
  }
  if (opt_style) {
    params['style'] = opt_style;
  }

  // To display POI symbols and labels:
  if (opt_pois) {
    params['pois'] = '';
  }

  incidentsProvider = new H.service.TrafficIncidentsProvider(incidents, undefined, opt_lg ? params : null);
  this.incidentsLayer_ = new H.map.layer.MarkerTileLayer(incidentsProvider);

  return {
    'normal': {
      'xbase': baseMT.createTileLayer('xbasetile', 'normal.day', tileSize, 'png8', params, 1, false),
      'base': baseMT.createTileLayer('basetile', 'normal.day', tileSize, 'png8', params, 1, false),
      'map': baseMT.createTileLayer('maptile', 'normal.day', tileSize, 'png8', params, 1, false),
      'traffic': trafficMT.createTileLayer('traffictile', 'normal.day', tileSize, 'png8', params, 1, false),
      'transit': baseMT.createTileLayer('maptile', 'normal.day.transit', tileSize, 'png8', params, 1, false),
      'panorama': panoMT.createTileLayer('rctile', 'normal.day', tileSize, 'png8', params, 1, false),
      'labels': baseMT.createTileLayer('labeltile', 'normal.day', tileSize, 'png', params, 1, false),
      'metaInfo': metaInfoService.createTileLayer(tileSize, opt_ppi === 320 ? 2 : 1, ['buildings'], params)
    },
    'satellite': {
      'xbase': aerialMT.createTileLayer('xbasetile', 'hybrid.day', tileSize, 'jpg', params, 1, true),
      'base': aerialMT.createTileLayer('basetile', 'hybrid.day', tileSize, 'jpg', params, 1, true),
      'map': aerialMT.createTileLayer('maptile', 'hybrid.day', tileSize, 'jpg', params, 1, true),
      'traffic': trafficMT.createTileLayer('traffictile', 'hybrid.day', tileSize, 'jpg', params, 1, true),
      'panorama': panoMT.createTileLayer('rctile', 'hybrid.day', tileSize, 'jpg', params, 1, true),
      'labels': aerialMT.createTileLayer('labeltile', 'hybrid.day', tileSize, 'png', params, 1, true)
    },
    'terrain': {
      'xbase': aerialMT.createTileLayer('xbasetile', 'terrain.day', tileSize, 'jpg', params, 1, false),
      'base': aerialMT.createTileLayer('basetile', 'terrain.day', tileSize, 'jpg', params, 1, false),
      'map': aerialMT.createTileLayer('maptile', 'terrain.day', tileSize, 'jpg', params, 1, false),
      'traffic': trafficMT.createTileLayer('traffictile', 'terrain.day', tileSize, 'jpg', params, 1, false),
      'panorama': panoMT.createTileLayer('rctile', 'terrain.day', tileSize, 'jpg', params, 1, false),
      'labels': aerialMT.createTileLayer('labeltile', 'terrain.day', tileSize, 'png', params, 1, false)
    },
    'incidents': this.incidentsLayer_
  };
};


/**
 * A map type is an object holding tile layers corresponding to a map type (e.g. 'normal', 'satellite' or
 * 'terrain'). A map type contains at least a {@code map} property which defines the basic map layer for
 * a given map type. In addition it can hold other map layers with the given style, e.g. {@code base},
 * {@code xbase}, {@code traffic} etc.
 *
 * @typedef {{
 *  map: (H.map.layer.TileLayer),
 *  xbase: (H.map.layer.TileLayer|undefined),
 *  base: (H.map.layer.TileLayer|undefined),
 *  traffic: (H.map.layer.TileLayer|undefined),
 *  panorama: (H.map.layer.TileLayer|undefined),
 *  labels: (H.map.layer.TileLayer|undefined)
 * }}
 * @property {H.map.layer.TileLayer} map the basic map tiles with all features and labels
 * @property {H.map.layer.TileLayer=} xbase map tiles without features and labels
 * @property {H.map.layer.TileLayer=} base map tiles without labels
 * @property {H.map.layer.TileLayer=} traffic map tiles with traffic flow highlighting
 * @property {H.map.layer.TileLayer=} transit map tiles with public transit lines highlighted
 * @property {H.map.layer.TileLayer=} panorama map tiles highlighting areas with HERE StreetLevel coverage
 * @property {H.map.layer.TileLayer=} labels transparent map tiles with labels only
 *
 * @publish
 */
H.service.MapType;
