goog.provide('H.service.PlacesService');

goog.require('H.service.AbstractRestService');
goog.require('H.service.Platform');



/**
 * Places service implements a low level places RestApi access.
 * Please refer to [Restful API documentation](http://developer.here.com/rest-apis/documentation/places)
 * for providing parameters and handling response objects.
 *
 * Entry point parameters are specified as objects as key:value pairs which are transformed to URL parameters.
 * Request headers can be specified along with entry point parameters.
 *
 * @example
 * var places = platform.getPlacesService();
 * places.search({
 *  'at': '52.5044,13.3909',
 *  'q': 'pizza',
 *  'Accept-Language': 'de'
 * }, function(response) {
 *  //handle response
 * }, function() {
 *  //handle network error
 * });
 *
 * @constructor
 * @param {H.service.PlacesService.Options=} opt_options
 * @extends {H.service.AbstractRestService}
 * @publish
 * @export
 * @publish
 */
H.service.PlacesService = function(opt_options) {
  'use strict';

  var options = H.service.mergeOptions(H.service.getOptions('places'), opt_options);
  options['callbackKey'] = 'callback';

  H.util.getSuperProto(H.service.PlacesService).constructor.call(this, options);
};
H.lang.inherits(H.service.PlacesService, H.service.AbstractRestService);


/**
 * List of available entry points
 * @enum {string}
 * @export
 * @publish
 */
H.service.PlacesService.EntryPoint = {
  'SEARCH': 'discover/search',
  'SUGGEST': 'suggest',
  'EXPLORE': 'discover/explore',
  'AROUND': 'discover/around',
  'HERE': 'discover/here',
  'CATEGORIES': 'categories/places'
};


/**
 * This is generic method to query places RestAPI.
 *
 * @param {string} entryPoint can be one of available entry points {@link H.service.PlacesService.EntryPoint}
 * i.e value of H.service.PlacesService.EntryPoint.SEARCH
 * @param {Object} entryPointParams parameter map key value pairs will be transformed into the
 * url key=value parametes. For entry point parameters description please refer to places
 * restful api [documentation](http://developer.here.com/rest-apis/documentation/places)
 * documentation for available parameters for chose entry point
 * @param {Function} onResult callback which is called when result is returned
 * @param {Function} onError callback which is called when error occured (i.e request timeout)
 * @return {H.service.JsonpRequestHandle} jsonp request handle
 *
 * @example
 * var places = platform.getPlacesService(),
 *     entryPoint = H.service.PlacesService.EntryPoint;
 * places.request(entryPoint.SEARCH, { 'at': '52.5044,13.3909', 'q': 'pizza'}, function(resp) {
 *  console.log(resp);
 * }, function(resp) {
 *  console.log('ERROR: '+resp);
 * });
 *
 * @export
 * @publish
 */
H.service.PlacesService.prototype.request = function(entryPoint, entryPointParams, onResult, onError) {
  'use strict';
  entryPointParams = entryPointParams || {};
  var url = this.getUrl()
      .clone()
      .addSubPath(entryPoint)
      .mergeQuery(entryPointParams);
  return this.restApiRequest(url, onResult, onError);
};


/**
 * Function triggers places api 'search' entry point. Please refer to
 * [documentation](http://developer.here.com/rest-apis/documentation/places/topics_api/resource-search.html)
 * for parameter specification and response handling.
 *
 * @param {H.service.ServiceParameters} searchParams places api search entry
 * point parameters please refer to places api documentation
 * @param {Function} onResult
 * @param {Function} onError
 * @return {H.service.JsonpRequestHandle} jsonp request handle
 * @export
 * @publish
 */
H.service.PlacesService.prototype.search = function(searchParams, onResult, onError) {
  'use strict';
  return this.request(H.service.PlacesService.EntryPoint.SEARCH, searchParams,
      onResult, onError);
};


