/**
 * This method should be used to follow hyperlinks available in results returned
 * by dicovery queries.
 *
 * @example
 * var places = platform.getPlacesService(),
 *     entryPoint = H.service.PlacesService.EntryPoint;
 * places.request(entryPoint.SEARCH, { 'at': '52.5044,13.3909', 'q': 'pizza'}, function(response) {
 *    var items = response.results.items;
 *    places.follow(items[0].href, function(resp) {
 *      console.log(resp);
 *    },
 *    function(resp) {
 *      console.log('ERROR: '+resp);
 *    });
 * }, function(resp) {
 *  console.log('ERROR: '+resp);
 * });
 *
 * @param {string} hyperlink
 * @param {Function} onResult
 * @param {Function} onError
 * @param {Object=} opt_additionalParameters additional parameters to send with request
 * @return {H.service.JsonpRequestHandle} jsonp resquest handle
 * @export
 * @publish
 */
H.service.PlacesService.prototype.follow = function(hyperlink, onResult, onError, opt_additionalParameters) {
  'use strict';
  var url = H.service.Url.parse(hyperlink);
  if (opt_additionalParameters) {
    url.mergeQuery(opt_additionalParameters);
  }
  return this.restApiRequest(url, onResult, onError);
};


/**
 * @typedef {{
 *   subDomain: (string|undefined),
 *   path: (string|undefined),
 *   baseUrl: (H.service.Url|undefined)
 * }}
 *
 * @property {string=} subDomain the sub-domain of the places service relative
 *  to the platform's base URL, default is {@code 'places'}
 * @property {string=} path the path of the places service, default is
 *  {@code 'places/v1'}
 * @property {H.service.Url=} baseUrl an optional base URL if it differs
 *  from the platform's default base URL
 * @publish
 */
H.service.PlacesService.Options;


//Storing default options
H.service.setOptions('places', {
  'subDomain': 'places',
  'path': 'places/v1'
});


/**
 * This method returns an implementation instance of
 * {@link H.service.PlacesService}
 * @return {H.service.PlacesService} a new places service instance
 * @export
 * @publish
 */
H.service.Platform.prototype.getPlacesService = function() {
  'use strict';
  return /** @type {H.service.PlacesService} */ (this.configure(
      new H.service.PlacesService()));
};
