goog.provide('H.service.Url');



/**
* This class represents a URL giving access to the individual parts
* that make up a URL,such as the scheme, host/domain, path, etc.
* Use the static parse method to populate a new URL object from a URL string.
* Be aware that URLs with user and password like "ftp://user:password@foo.bar/" are not supported!
* @constructor
* @param {string} scheme the URL scheme (e.g. "http" or "https" or "mailto")
* @param {string} host the host (or domain) part of the URL
* @param {string=} opt_path the path following the host pointing to a
*  resource
* @param {Object=} opt_params the query string parameters of this
*  URL
* @param {number=} opt_port The port of the host on which the host
*  listens. If a string is passed it must be convertible to an integer.
* @param {string=} opt_anchor an optional anchor part of the URL (usually
*  preceded by '#');
*
* @throws {Error} Throws an error if either the 'scheme' and 'host' are
*  undefined, if 'opt_params' was specidied but cannot be processed or if the port
*  is defined but cannot be converted to a number.
* @export
* @publish
*/
H.service.Url = function(scheme, host, opt_path, opt_params, opt_port, opt_anchor) {
  'use strict';

  if (scheme && host) {
    this.setScheme(scheme);
    this.setHost(host);
    this.setPath(opt_path);
    this.setPort(opt_port);
    this.setAnchor(opt_anchor);
    this.setQuery(opt_params);
  } else {
    throw new Error('Parameters "scheme" and "host" must be specified');
  }
};


/**
 * A helper method which translates a URL into a full qualified URL
 * @private
 * @param {string} url The URL to full qualify
 * @param {string=} opt_baseURL The underlying base URL to use.
 *        If omitted, the base URL of the document which loaded the API is used.
 * @return {string} the full qualified URL
 */
H.service.Url.fullQualifyURL_ = function(url, opt_baseURL) {
  var doc = document,
      origBase,
      origHRef,
      htmlHead,
      base,
      link = doc.createElement('a'),
      fullQualifiedURL;

  // Save the current original state of the document and set the new base URL:
  if (opt_baseURL) {
    origBase = doc.getElementsByTagName('base')[0];
    origHRef = origBase && origBase.href;
    htmlHead = doc.head;
    base = origBase || htmlHead.appendChild(doc.createElement('base'));
    base.href = opt_baseURL;
  }
  // Resolve the URL:
  link.href = url;
  fullQualifiedURL = link.href;

  // Restore the original state of the document if needed:
  if (opt_baseURL) {
    if (origBase) {
      origBase.href = origHRef;
    } else {
      htmlHead.removeChild(base);
    }
  }

  return fullQualifiedURL;
};


/**
 * A helper method to get the base URL of an document
 * @private
 * @param {Document=} opt_doc The document to inspect, if omitted, the API document is used
 * @return {string} the base URL
 */
H.service.Url.getBaseURI_ = function(opt_doc) {
  var doc = opt_doc || document,
      baseURI,
      base;
  if (!(baseURI = doc.baseURI)) {
    base = doc.getElementsByTagName('base')[0];
    baseURI = base && base.href || doc.location.href;
  }
  return baseURI;
};


/**
* This function parses a URL string and returns a H.service.Url object. The URL
*  string must contain at least a scheme and a host.
* @publish
* @export
* @param {string} url The URL string to parse.
* @param {string=} opt_baseURL The base URL to use to resolve relative URLs.
*        If ommited the base URL of the document which loaded the API is taken.
* @return {H.service.Url} the parsed URL object
*/
H.service.Url.parse = function(url, opt_baseURL) {
  'use strict';

  url = H.service.Url.fullQualifyURL_(url, opt_baseURL);
  var parseRx = new RegExp(
                           '(?:(\\w+)://)?(?:([^:]+):([^@/]*)@)?([^/:]+)?' +
                           '(?:[:]{1}([0-9]+))?(/[^?#]*)?(\\?[^#]+)?(#.*)?'),
      parsedParts = parseRx.exec(url),
      scheme = parsedParts[1],
      user = parsedParts[2],
      host = parsedParts[4],
      port = parsedParts[5],
      path = parsedParts[6],
      params = parsedParts[7],
      anchor = parsedParts[8],
      hostSplit;
  // A workaround for the fact that the regex doesn't parse user names without passwords correctly:
  if (!user && host && /@/.test(host)) {
    host = host.split('@')[1];
  }

  return new H.service.Url(
      scheme,
      host,
      (path && path.length > 0) ? path.substr(1) : '',
      H.service.Url.parseQuery_(params),
      isNaN(+port) ? undefined : +port,
      (anchor && anchor.length > 0) ? anchor.substr(1) : ''
  );
};


/**
 * Clones this URL object. Optionally, mutations can be passed to this function
 * to modify properties of the cloned object.
 * Note that URL parameters are not replaced but merged with the parameters of
 * this instance.
 *
 * @return {H.service.Url} the clone of the URL object
 * @export
 * @publish
 */
H.service.Url.prototype.clone = function() {
  'use strict';

  return new H.service.Url(
      this.scheme_,
      this.host_,
      this.path_,
      this.cloneQuery_(),
      this.port_,
      this.anchor_
  );
};


/**
 * This function sets the scheme of this URL object.
 * @param {string} scheme the new scheme
 * @return {H.service.Url} this URL object
 * @export
 * @publish
 */
H.service.Url.prototype.setScheme = function(scheme) {
  'use strict';

  if (!scheme || (typeof scheme !== 'string')) {
    throw new Error('The "scheme" parameter must be a non-empty string.');
  }
  this.scheme_ = scheme;
  return this;
};


/**
 * This function returns the scheme of this Url object.
 * @return {string} the scheme (for example 'http')
 * @export
 * @publish
 */
H.service.Url.prototype.getScheme = function() {
  'use strict';
  return this.scheme_;
};


/**
 * This function sets the host of this URL object.
 * @param {string} host the new host
 * @return {H.service.Url} this URL object
 * @throws {Error} Throws an error if the host is not a string, empty or if
 *  the host name contains illegal characters (i.e. starts with '-' or '.' or
 *  ends with '.').
 * @export
 * @publish
 */
H.service.Url.prototype.setHost = function(host) {
  'use strict';

  if (!host ||
      (typeof host !== 'string') ||
      host.charAt(0) === '-' ||
      host.charAt(0) === '.' ||
      host.charAt(host.length - 1) === '.') {
    throw new Error('The "host" parameter is not a valid host name.');
  }
  this.host_ = host;
  return this;
};


/**
 * This function returns the host name of this Url object.
 * @return {string} the host (for example 'api.here.com')
 * @export
 * @publish
 */
H.service.Url.prototype.getHost = function() {
  'use strict';
  return this.host_;
};


/**
 * This function sets the path of this URL object.
 * @param {string|undefined} path the new path or a boolean to clear the path
 * @return {H.service.Url} this URL object
 * @export
 * @publish
 */
H.service.Url.prototype.setPath = function(path) {
  'use strict';
  this.path_ = path || undefined;
  return this;
};


/**
 * This function returns the path part of this Url object.
 * @return {string|undefined} the path (for example 'myresources/resource.html')
 * @export
 * @publish
 */
H.service.Url.prototype.getPath = function() {
  'use strict';
  return this.path_;
};


/**
 * This function sets the specified parameters for this URL object. Keys in this object, which
 * are associated with undefined values will be treated as query string
 * parameters with no value.
 * @param {Object|undefined} params a hash of query string parameters specifying the
 *  parameters to be set.or a boolean to clear the parameters.
 *
 * @return {H.service.Url} this URL object
 * @export
 * @publish
 */
H.service.Url.prototype.setQuery = function(params) {
  'use strict';
  if (params) {
    if (typeof params == 'object') {
      this.params_ = params;
    }
    else {
      throw new Error('Parameter "params" must be an object.');
    }
  } else {
    this.params_ = {};
  }

  return this;
};


/**
 * This function returns a boolean value indicating whether there are any query string
 * parameter associated with this URL.
 * @return {boolean} true if there are parameters, false if none are present
 * @export
 * @publish
 */
H.service.Url.prototype.hasQuery = function() {
  'use strict';
  return !this.isQueryEmpty_();
};


/**
 * This function returns the query object of this Url object.
 * @return {Object} the query object
 * @export
 * @publish
 */
H.service.Url.prototype.getQuery = function() {
  'use strict';
  return this.params_;
};


/**
 * This function sets the port number of this URL object.
 * @param {number|undefined} port the new port number or a boolean to clear the
 *  port number (note that 0 is also a boolean)
 * @return {H.service.Url} this URL object
 * @throws {Error} Throws an error if the parameter cannot be converted to a
 *  number.
 * @export
 */
H.service.Url.prototype.setPort = function(port) {
  'use strict';
  if (port && isNaN(parseInt(port, 10))) {
    throw new Error('Parameter "port" must be a number or undefined');
  }

  this.port_ = +/** @type {number}*/(port) || undefined;
  return this;
};


/**
 * This function returns the port number of this Url object.
 * @return {number|undefined} the port number
 * @export
 */
H.service.Url.prototype.getPort = function() {
  'use strict';
  return this.port_;
};


/**
 * This function sets the anchor of this URL object.
 * @param {string|boolean|undefined} anchor the new anchor or undefined to clear the
 *  anchor
 * @return {H.service.Url} this URL object
 * @export
 * @publish
 */
H.service.Url.prototype.setAnchor = function(anchor) {
  'use strict';
  this.anchor_ = anchor || undefined;
  return this;
};


/**
 * This function returns the anchor of this Url object.
 * @return {string|undefined} the anchor
 * @export
 * @publish
 */
H.service.Url.prototype.getAnchor = function() {
  'use strict';
  return this.anchor_;
};


/**
 * This function merges the provided parameters into this URL's existing parameters. Key-value
 * pairs which are defined in the argument and this URL's parameters will be
 * overwritten. Key-value pairs which are defined in the argument and are not
 * defined in this URL's parameters will be added. Prototype properties and
 * function properties will not be merged.
 * @example
 * var url = new H.service.Url('http', 'api.here.com');
 * url.setQuery({ 'foo': 'bar' });
 * url.mergeQuery({ 'foo': 'baz', 'bar': 'foo' });
 *
 * var newQuery = url.getQuery();
 * newQuery['foo'] === 'bar' // false
 * newQuery['foo'] === 'baz' // true
 * newQuery['bar'] === 'foo' // true
 *
 * @param {Object} other the parmeters to be merged into this URL's query
 *  string parameters
 * @return {H.service.Url} this URL object
 * @throws {Error} Throws an error if the argument is not of type 'object'.
 * @export
 * @publish
 */
H.service.Url.prototype.mergeQuery = function(other) {
  'use strict';
  if (typeof other !== 'object') {
    throw new Error('Parameter "other" must be an object.');
  }
  this.mergeQuery_(this.params_, other);

  return this;
};


/**
 * This function adds a sub-domain to the host of this URL object.
 * @param {string} subDomain the sub domain (non-empty string) to be added
 * @return {H.service.Url} this URL object
 * @export
 * @publish
 */
H.service.Url.prototype.addSubDomain = function(subDomain) {
  'use strict';

  if (!subDomain || (typeof subDomain !== 'string')) {
    throw new Error('Parameter "subDomain" must be a non-empty string');
  }
  if (subDomain.substr(subDomain.length - 1) === '.') {
    //clean trailing '.'
    subDomain = subDomain.substr(0, subDomain.length - 1);
  }
  return this.setHost(subDomain + '.' + this.host_);
};


/**
 * This function adds a sub-path to this URL's path
 * @param {string} subPath the path to be added
 * @return {H.service.Url} this URL object
 * @export
 * @publish
 */
H.service.Url.prototype.addSubPath = function(subPath) {
  'use strict';

  if (!subPath || (typeof subPath !== 'string')) {
    throw new Error('Parameter "subPath" must be a non-empty string');
  }
  return this.setPath((this.path_ ? this.path_ + '/' : '') + subPath);
};


/**
 * This function formats this URL object to a full URL string.
 * @return {string} the URL's string representation
 * @export
 * @publish
 */
H.service.Url.prototype.toString = function() {
  'use strict';

  return [
    this.scheme_ + '://',
    this.host_,
    (this.port_ ? ':' + this.port_ : ''),
    (this.path_ || !this.isQueryEmpty_() || this.anchor_ ? '/' : ''),
    (this.path_ ? encodeURI(this.path_) : ''),
    (this.queryToString_()),
    (this.anchor_ ? '#' + encodeURIComponent(this.anchor_) : '')
  ].join('');
};


/**
 * This function clones this query string object. Optionally a hash of parameter mutations can
 *  be passed to modify the clone's parameters.
 * @return {Object} a new object containing the same key-value pairs as this
 *  URL object
 * @private
 * @export
 */
H.service.Url.prototype.cloneQuery_ = function() {
  'use strict';

  var clonedParams = {},
      key;
  for (key in this.params_) {
    clonedParams[key] = this.params_[key];
  }
  return clonedParams;
};


/**
 * Merges the provided query parameters into this parameter collection. Existing
 * query parameters will be overridden with the values of the argument and
 * non-existing ones will be added. Prototype properties and function
 *  properties will not be merged.
 * @param {Object} params the original parameters to merge to
 * @param {Object} other the parameters to merge
 * @return {Object} the merged parameters
 * @private
 */
H.service.Url.prototype.mergeQuery_ = function(params, other) {
  'use strict';

  var key;
  for (key in other) {
    //filter prototype properties and functions
    if (other.hasOwnProperty(key) && (typeof other[key] !== 'function')) {
      params[key] = other[key];
    }
  }
  return params;
};


/**
 * Returns the string representation of this Url's query string object.
 * @return {string} the formatted URL query string
 * @private
 */
H.service.Url.prototype.queryToString_ = function() {
  'use strict';

  var list = [],
      key,
      params = this.params_,
      value;
  for (key in params) {
    if (params.hasOwnProperty(key) && typeof params[key] !== 'function') {
      value = this.params_[key];
      list.push(
          encodeURIComponent(key) +
          (value === undefined ?
              '' :
              '=' + encodeURIComponent(value)
          )
      );
    }
  }
  return list.length > 0 ? '?' + list.join('&') : '';
};


/**
 * Determines whether any query string parameters are registered with
 * this URL object
 * @private
 * @return {boolean} a boolean value indicating whether this URL object's query
 *  string is empty
 */
H.service.Url.prototype.isQueryEmpty_ = function() {
  'use strict';

  var key,
      i = 0;
  for (key in this.params_) {
    if (this.params_.hasOwnProperty(key) &&
        typeof this.params_[key] !== 'function') {
      i++;
      if (i > 0) break;
    }
  }
  return !i;
};


/**
 * Creates a populated Url.QueryString object from a query string part of a URL
 *  string.
 * @param {string} queryString the query string (preceded by a "?" character in
 *  URLs).
 * @return {Object} an object populated with the parameter entries as
 *  key-value pairs.
 * @private
 */
H.service.Url.parseQuery_ = function(queryString) {
  'use strict';

  var urlParams = {},
      parseRx,
      paramPart;

  if (queryString) {
    parseRx = new RegExp('([?&]?([^=&+]+))(=([^&]+))?', 'g');
    while ((paramPart = parseRx.exec(queryString))) {
      urlParams[decodeURIComponent(paramPart[2])] = paramPart[4] ?
          decodeURIComponent(paramPart[4]) : undefined;
    }
  }

  return urlParams;
};
