goog.provide('H.service.MapTileProvider');

goog.require('H.service.MapTileCopyrights');
goog.require('H.service.util');



/**
 * This class represents a map tile provider which requests map tiles from
 * a platform map tile service.
 *
 * @extends {H.map.provider.ImageTileProvider}
 * @constructor
 *
 * @param {H.service.MapTileService} service the map tile service which serves the tiles
 * @param {string} tileType the map tile type
 * @param {string} scheme the map scheme
 * @param {number} tileSize the map tile size
 * @param {string} format the map tile image format
 * @param {H.service.ServiceParameters=} opt_additionalParameters an additional set of URL parameters
 */
H.service.MapTileProvider = function(service, tileType, scheme, tileSize, format, opt_additionalParameters) {
  'use strict';
  var that = this,
      params = opt_additionalParameters ? opt_additionalParameters : {},
      tileBaseUrl = service.getUrl()
        .clone()
        .addSubPath(tileType + '/' + service.getVersion() + '/' + scheme)
        .mergeQuery(params),
      shards = service.getShards();

  H.map.provider.ImageTileProvider.call(this, {
    'tileSize': tileSize,
    'max': (tileSize == 512) ? 19 : 20,
    'min': (tileSize == 512) ? 2 : 0,
    'getURL': function(x, y, z) {
      var tileUrl = that['tileBaseUrl_'].clone();
      if (shards) {
        tileUrl.addSubDomain(shards[(z + y + x) % shards.length]);
      }
      tileUrl.addSubPath(z + '/' + x + '/' + y + '/' + tileSize + '/' + format);
      return tileUrl.toString();
    }
  });

  /**
   * @private
   * @type {H.servive.MapTileService}
   */
  this['service_'] = service;

  /**
   * @private
   * @type {H.service.Url}
   */
  this['tileBaseUrl_'] = tileBaseUrl;

  /**
   * @private
   * @type {string}
   */
  this['tileType_'] = tileType;

  /**
   * @private
   * @type {string}
   */
  this['scheme_'] = scheme;

  /**
   * @private
   * @type {Object.<string, string>}
   */
  this['params_'] = params;

  /**
   * @private
   * @type {string}
   */
  // TODO renamed copyrightType_ -> _copyrightType_ due to name clash with parent class
  //    it must be solved in generic way, otherwise code becomes fragile MAPSJS-69
  this['copyrightKey_'] = scheme.split('.')[0];

  this['updateAndReload_'] = H.lang.bind(this['updateAndReload_'], this);

  service.addEventListener('versionupdate', this['updateAndReload_']);
};
H.lang.inherits(H.service.MapTileProvider, H.map.provider.ImageTileProvider);


/**
 * Creates a unique string for the provider.
 *
 * @param {H.service.MapTileService} service the map tile service which serves the tiles
 * @param {string} tileType the map tile type
 * @param {string} scheme the map scheme
 * @param {number} tileSize the map tile size
 * @param {string} format the map tile image format
 * @param {H.service.ServiceParameters=} opt_additionalParameters an additional set of URL parameters
 * @return {number} the unique string for this provider
 */
H.service.MapTileProvider.hash = function(service, tileType, scheme, tileSize, format, opt_additionalParameters) {
  return H.service.util.hash(
      service.getUrl().clone()
                     .addSubPath(tileType + '/' + service.getVersion() + '/' + scheme + '/' + tileSize + '/' + format)
                     .mergeQuery(opt_additionalParameters ? opt_additionalParameters : {})
                     .toString());
};


/**
 * Disposes of this map tile provider instance
 */
H.service.MapTileProvider.prototype.disposeInternal = function() {
  'use strict';
  H.map.provider.ImageTileProvider.prototype.disposeInternal.call(this);
  this['service_'].removeEventListener('versionupdate', this['updateAndReload_']);
};


/**
 * @override
 */
H.service.MapTileProvider.prototype.getCopyrights = function(bounds, level) {
  'use strict';
  return this['service_'].getCopyrights().query(this['copyrightKey_'], level, bounds);
};


/**
 * Callback is invoked when tile version information arrives from the tile server.
 */
H.service.MapTileProvider.prototype['updateAndReload_'] = function() {
  'use strict';
  var service = this['service_'];
  this['tileBaseUrl_'] = service.getUrl()
      .clone()
      .addSubPath(this['tileType_'] + '/' + service.getVersion() + '/' + this['scheme_'])
      .mergeQuery(this['params_']);
  this.reload(false);
};
