goog.provide('H.service.TrafficIncidentsProvider');

goog.require('H.service.TrafficIncidentsIcons');
goog.require('H.service.TrafficIncidentsProvider_jsdoc');
goog.require('H.service.util');



/**
 * This class represents traffic incidents provider which requests traffic incidents data
 * from platform traffic incidents service and converts it to map objects.
 * @extends {H.map.provider.MarkerTileProvider}
 * @constructor
 *
 * @param {H.service.TrafficIncidentsService} service traffic incidents service that serves incidents data
 * @param {number=} opt_refreshTime argument indicates timeframe when provider tiles should be updated (in ms)
 *      defaults to {@code 3} minutes
 * @param {H.service.ServiceParameters=} opt_additionalParameters an additional set of URL parameters
 *
 * @export
 * @publish
 */
H.service.TrafficIncidentsProvider = function(service, opt_refreshTime, opt_additionalParameters) {
  'use strict';
  var that = this,
      params = opt_additionalParameters ? opt_additionalParameters : {
        'criticality': 'major,critical'
      };

  if (!(service instanceof H.service.TrafficIncidentsService)) {
    throw new Error('IllegalArgument: service');
  }

  this.requestData_ = H.lang.bind(this.requestData_, this);

  H.map.provider.MarkerTileProvider.call(this, {
    'max': 20,
    'min': 8,
    'requestData': this.requestData_
  });

  /**
   * @private
   * @type {H.service.TrafficIncidentsIcons}
   */
  this.icons_ = new H.service.TrafficIncidentsIcons();

  /**
   * @private
   * @type {H.service.TrafficIncidentsService}
   */
  this.service_ = service;

  /**
   * @private
   * @type {Object.<string, string>}
   */
  this.params_ = params;

  this.reload = H.lang.bind(this.reload, this);
  this.refreshHandle_ = setInterval(this.reload, opt_refreshTime || 180000);
};
H.lang.inherits(H.service.TrafficIncidentsProvider, H.map.provider.MarkerTileProvider);


/**
 * @private
 * @type {Object}
 */
H.service.TrafficIncidentsProvider.prototype.criticalityDict_ = {
  'minor': 'HIGH',
  'low impact': 'HIGH',
  'major': 'VERYHIGH',
  'critical': 'BLOCKING'
};


/**
 * Implementation of H.map.provider.MarkerTileProvider requestData function
 * @private
 * @param {number} x x tile coordinate
 * @param {number} y y tile coordinate
 * @param {number} z zoom level
 * @param {function (Array.<H.map.AbstractMarker>)} onSuccess
 * @param {function()} onError
 * @return {H.util.ICancelable}
 */
H.service.TrafficIncidentsProvider.prototype.requestData_ = function(x, y, z, onSuccess, onError) {
  'use strict';
  var that = this,
      criticalityDict = this.criticalityDict_,
      icons = this.icons_,
      req;

  req = this.service_.requestIncidentsByTile(x, y, z, function(result) {
    var data = [],
        trafficItem = 'TRAFFIC_ITEM',
        items = result[trafficItem + 'S'],
        item,
        location,
        typeDesc,
        marker,
        icon,
        i;

    if (items) {
      items = items[trafficItem];
      i = items.length;
      while (i--) {
        item = items[i];

        typeDesc = item[trafficItem + '_TYPE_DESC'];
        switch (typeDesc) {
          case 'ACCIDENT':
          // expected switch fall-through
          case 'CONGESTION':
            icon = typeDesc;
            break;
          case 'CONSTRUCTION':
            icon = 'ROADWORKS';
            break;
          default:
            icon = 'OTHER';
        }
        icon = icons.getIcon(icon,
            criticalityDict[item['CRITICALITY']['DESCRIPTION']]);

        location = item['LOCATION']['GEOLOC']['ORIGIN'];
        marker = new H.map.Marker(/** @type {H.geo.IPoint} */({
          'lat': location['LATITUDE'],
          'lng': location['LONGITUDE']
        }), /** @type {H.map.Marker.Options} */ ({
          'provider': that,
          'icon': icon
        }));
        marker.setData(item);
        data.push(marker);
      }
    }
    onSuccess(data);

  }, onError, this.params_);

  return /** @type {H.util.ICancelable} */ (req);
};


/**
 * Disposes of this map tile provider instance
 */
H.service.TrafficIncidentsProvider.prototype.dispose = function() {
  'use strict';
  H.map.provider.MarkerTileProvider.prototype.dispose.call(this);
  clearInterval(this.refreshHandle_);
};
