goog.provide('H.service.TrafficIncidentsService');

goog.require('H.service.AbstractRestService');
goog.require('H.service.Platform');



/**
 * TrafficIncindentsService provides functionality to the low level traffic incidents api
 * [Traffic API documentation](http://developer.here.com/rest-apis/documentation/traffic/topics/overview.html)
 * were it is possible to retrieve traffic incident information on a tile basis
 *
 * @constructor
 * @param {H.service.TrafficIncidentsService.Options=} opt_options
 * @extends {H.service.AbstractRestService}
 * @export
 * @publish
 */
H.service.TrafficIncidentsService = function(opt_options) {
  'use strict';
  var options = H.service.mergeOptions(H.service.getOptions('trafficIncidents'), opt_options);
  options['callbackKey'] = 'jsoncallback';

  H.util.getSuperProto(H.service.TrafficIncidentsService).constructor.call(this, options);
};
H.lang.inherits(H.service.TrafficIncidentsService, H.service.AbstractRestService);


/**
 * This method requests traffic incidents based on the service parameters provided.
 * @param {H.service.ServiceParameters} serviceParams
 * @param {function(H.service.ServiceResult)} onResponse
 * @param {function()} onError
 * @return {H.service.JsonpRequestHandle}
 *
 * @export
 * @publish
 */
H.service.TrafficIncidentsService.prototype.requestIncidents = function(serviceParams, onResponse, onError) {
  'use strict';
  var url = this.getUrl()
      .clone()
      .addSubPath('incidents.json')
      .mergeQuery(serviceParams);
  return this.restApiRequest(url, onResponse, onError);
};


/**
 * This method requests traffic incident information by tile coordinates
 *
 * @param {number} x tile column number
 * @param {number} y tile row number
 * @param {number} z zoom level
 * @param {function(H.service.ServiceResult)} onResponse callback to handle service resposne
 * @param {function()} onError callback to habdle communication error
 * @param {H.service.ServiceParameters=} opt_serviceParams optional service parameters to be added to the request
 * @return {H.service.JsonpRequestHandle}
 *
 * @publish
 * @export
 */
H.service.TrafficIncidentsService.prototype.requestIncidentsByTile = function(x, y, z, onResponse,
    onError, opt_serviceParams) {
  'use strict';
  var url = this.getUrl()
      .clone()
      .addSubPath(['incidents/json', z, x, y].join('/'))
      .mergeQuery(opt_serviceParams || {});
  return this.restApiRequest(url, onResponse, onError);
};


/**
 * @typedef {{
 *   subDomain: (string|undefined),
 *   path: (string|undefined),
 *   baseUrl: (H.service.Url|undefined)
 * }}
 *
 * @property {string=} subDomain the sub-domain of the traffic incidents service relative
 *  to the platform's base URL, default is {@code 'traffic'}
 * @property {string=} path the path of the traffic incidents service, default is
 *  {@code 'traffic/6.1'}
 * @property {H.service.Url=} baseUrl an optional base URL if it differs
 *  from the platform's default base URL
 * @publish
 */
H.service.TrafficIncidentsService.Options;


//Storing default options
H.service.setOptions('trafficIncidents', {
  'subDomain': 'traffic',
  'path': 'traffic/6.1'
});


/**
 * This method returns an implementation instance of
 * {@link H.service.TrafficIncidentsService}
 * @return {H.service.TrafficIncidentsService} a new places service instance
 * @export
 * @publish
 */
H.service.Platform.prototype.getTrafficIncidentsService = function() {
  'use strict';
  return /** @type {H.service.TrafficIncidentsService} */ (this.configure(
      new H.service.TrafficIncidentsService()));
};
