goog.provide('H.service.jsonp');

goog.require('H.service.Url');


/**
 * Sends a JSON-P request and invokes the provided callback once the service
 *  responds.
 * @param {H.service.JsonpOptions} options the options for this JSON-P request
 * @return {H.service.JsonpRequestHandle} the handle of this request
 *
 * @export
 */

H.service.jsonp = function(options) {
  'use strict';

  if (!options.url || !options.callback) {
    throw new Error(
        'Parameter "options" must specify at least a URL and a callback.'
    );
  } else if (typeof options.callback !== 'function') {
    throw new Error('Parmaters options.callback must be a function');
  }

  //this has become some ugly piece of code but it is fairly compact:
  //-----------------------------------------------------------------

  // Passing the document with the options allows for using multiple documents
  // as well as mocking the document for unit testing
  var doc = options.doc || document,
      head = doc.getElementsByTagName('head')[0],
      script = doc.createElement('script'),
      url = options.url instanceof H.service.Url ?
          options.url :
          H.service.Url.parse(options.url, options.baseURL),
      //an ID to refer back to the request later on
      id = H.service.jsonp.responseId_++,
      //checking for a callback key or setting default
      callbackKey = options.callbackKey || 'callback',
      //getting a local short-cut to the callback function
      callback = options.callback,
      //the request handle object
      requestHandle = {},
      timeoutHandle = null;

  /**
   * @param {string=} opt_failure
   */
  function cleanup(opt_failure) {
    //clean up DOM
    head.removeChild(script);
    //make sure the callback does not accidentally get called again
    //check if a timeout was set and clear that
    timeoutHandle && clearTimeout(timeoutHandle);
    //remore the handler function from the registry
    delete H.service.jsonp.requests[id];
    //if we have a failure we should err back
    if (opt_failure) {
      options.errback && options.errback.call(requestHandle, opt_failure);
    }
    //if we don't have a failure all is good :)
  }

  function onResponse(data) {
    //get a temporary reference to the actual callback function
    // a) clean up replaces callback with an empty function to prevent
    //    calling it multiple times
    // b) clean up should have happend before we do anything else
    var _callback = callback;
    //clean up with no parameter (i.e. success)
    cleanup();
    //finally call the actual callback with context
    _callback.call(requestHandle, data);
  }

  //we add the ID to the handle directly (just to be able to see it from
  //the outside
  requestHandle['id'] = id;
  //we expose a cancel function on the object
  requestHandle['cancel'] = function() { cleanup('cancelled'); };

  //if a timeout was not set to 0
  if (options.timeout !== 0) {
    //we set a timeout of either options.timeout milliseconds or default
    //(30 seconds)
    timeoutHandle = setTimeout(
        function() { cleanup('timeout'); },
        options.timeout || 30000);
  }

  //finally we register the onResponse function globally
  H.service.jsonp.requests[id] = onResponse;
  //do some touching up of the script tag
  script.type = 'text/javascript';
  //set the source
  //  Note that we do not use the params collection of the H.service.Url.
  //  This is because params will automatically be encoded and some services
  //  (our maptiler for example) do not decode before writing it to the content.
  //  E.g.:
  //    .../maptile/2.1/info/callback_func=mysjonpcallback[12345]
  //  becomes:
  //    .../maptile/2.1/info/callback_func=mysjonpcallback%5B12345%5D
  //  and comes back as JavScript:
  //    mysjonpcallback%5B12345%5D( '{ "response" :
  //  which is not valid JavaScript
  script.src = url.toString() +
      (url.hasQuery() ? '&' : '?') +
      callbackKey + '=' +
      (options.preventCallbackEncoding ?
          'H.service.jsonp.handleResponse(' + id + ')' :
          encodeURI('H.service.jsonp.handleResponse(' + id + ')'));
  //and append
  head.appendChild(script);

  //return the handler to allow cancelling.
  return requestHandle;
};


/**
 * This method searches returns handler for incomming
 * response.
 * @param {number} callbackId
 * @export
 * @return {Function}
 */
H.service.jsonp.handleResponse = function(callbackId) {
  'use strict';
  var callback = H.service.jsonp.requests[callbackId];
  //return dummy function if callback was cancelled or timeout occured
  //and response camed in anymway after
  return callback ? callback : function() {};
};


/**
 * internal response ID counter
 * @private
 */
H.service.jsonp.responseId_ = 0;


/**
 * @dict
 */
H.service.jsonp.requests = {};


/**
 * @typedef {Object}
 * @property {number} id the ID associated internally with this request
 * @property {function()} cancel this function cancels the request and invokes
 *  the errback function
 * @publish
 */
H.service.JsonpRequestHandle;


/**
 * @typedef {{
 *   url: (string|H.service.Url),
 *   baseURL: (string|undefined),
 *   callback: function(*),
 *   errback: (function(string=)|undefined),
 *   timeout: (number|undefined),
 *   callbackKey: (string|undefined),
 *   preventCallbackEncoding: (boolean|undefined),
 *   doc: (HTMLDocument|undefined)
 * }}
 * @property {(string|H.service.Url)} url
 *           The URL of the JSON-P service to be called
 * @property {number=} baseURL
 *           The base URL to use to resolve relative URLs.
 *           If ommited the base URL of the document which loaded the API is taken
 * @property {function(*)} callback
 *           The function to be called once the service responds
 * @property {number=} timeout
 *           The number of milliseconds to wait before assuming a timeout (0 means no timeout)
 * @property {function(string)=} errback
 *           The function to be called if an error occurs. The string can have two values: 'timeout' and 'cancelled'
 * @property {string=} callbackKey
             The name of the JSON-P callback parameter function
 * @property {boolean} preventCallbackEncoding
 *           Indicates whether the callback value should not be URL encoded
 */
H.service.JsonpOptions;
