goog.provide('H.service.util');


/**
 * String-hash function ported from Java
 * @param {string} str
 * @return {number}
 */
H.service.util.hash = function(str) {
  'use strict';
  var hash = 0,
      i,
      chr,
      len = str.length;

  if (len === 0) {
    return hash;
  }

  for (i = 0; i < len; i++) {
    chr = str.charCodeAt(i);
    hash = ((hash << 5) - hash) + chr;
    hash |= 0;
  }

  return hash;
};


/**
 * Global object
 */
H.service.util.global = Function('return this')();
