goog.provide('H.service.AbstractRestService');


goog.require('H.service.IConfigurable');
goog.require('H.service.Url');
goog.require('H.service.jsonp');



/**
 * Abstract rest service class
 * @param {H.service.AbstractRestService.Options=} opt_options
 * @constructor
 * @implements {H.service.IConfigurable}
 * @export
 * @publish
 */
H.service.AbstractRestService = function(opt_options) {
  'use strict';
  var options = opt_options || {};

  /**
   * @type {string}
   * @private
   */
  this.subDomain_ = options['subDomain'];

  /**
   * @type {string}
   * @private
   */
  this.path_ = options['path'];

  /**
   * @type {string}
   * @private
   */
  this.callbackKey_ = options['callbackKey'];

  /**
   * @type {H.service.Url}
   * @private
   */
  this.baseUrl_ = options['baseUrl'];
};


/**
 * @override
 * @export
 */
H.service.AbstractRestService.prototype.configure = function(appId, appCode, useHTTPS, useCIT, opt_baseUrl) {
  'use strict';
  this.url_ = (this.baseUrl_ || opt_baseUrl).clone();
  if (this.subDomain_) {
    this.url_.addSubDomain(this.subDomain_);
  }
  if (this.path_) {
    this.url_.setPath(this.path_);
  }
  return this;
};


/**
 * Returns this service's URL according to it's configuration.
 * @return {H.service.Url} the URL of this service
 */
H.service.AbstractRestService.prototype.getUrl = function() {
  'use strict';

  return this.url_;
};


/**
 * This method fires request to actual rest service
 * @param {H.service.Url} url
 * @param {Function} onResult
 * @param {Function} onError
 * @return {H.service.JsonpRequestHandle}
 * @protected
 */
H.service.AbstractRestService.prototype.restApiRequest = function(url, onResult, onError) {
  'use strict';

  if (!goog.isFunction(onResult)) {
    throw new Error('onResult callback needed');
  }

  if (!goog.isFunction(onError)) {
    throw new Error('onError callback needed');
  }

  return H.service.jsonp({
    url: url,
    callback: function(data) {
      onResult(data);
    },
    errback: function(failure) {
      onError(new Error('[' + failure + '] ' + url + ' request failed'));
    },
    callbackKey: this.callbackKey_
  });
};


/**
 * @typedef {{
 *   subDomain: (string|undefined),
 *   path: (string|undefined),
 *   callbackKey: (string|undefined)
 * }}
 * @property {string=} subDomain
 * @property {string=} path
 * @property {string=} callbackKey
 */
H.service.AbstractRestService.Options;
