goog.provide('H.service.metaInfo.Service');


goog.require('H.service.IConfigurable');
goog.require('H.service.Platform');
goog.require('H.service.jsonp');
goog.require('H.service.util');



// TODO: ATTENTION! the following code is a copy-paste from MapTileService.
// The fact is that there's no clearcut way to share functionality between
// maptile service and meta info tileservice without refactoring half of the platform.
// we MUST thoroughly review our take on the platform and adjust accordingly
/**
 * This class encapsulates a
 * [Metainfo Tile](http://developer.here.com/rest-apis/documentation/enterprise-map-tile/topics/tile-metainfo.html)
 * end point of the HERE Map Tile API.
 *
 * An instance of this class can be retrieved by calling the factory method on
 * a platform instance. {@link H.service.Platform#getMetaInfoTileService}.
 * @publish
 * @export
 * @implements {H.service.IConfigurable}
 * @constructor
 * @extends {H.util.EventTarget}
 * @param {H.service.metaInfo.Service.Options=} opt_options additional service parameters
 */
H.service.metaInfo.Service = function(opt_options) {
  'use strict';
  var options = H.service.mergeOptions(H.service.getOptions('maptile'), opt_options || {});

  H.util.EventTarget.call(this);

  this.baseUrl_ = options['baseUrl'];
  this.path_ = options['path'];
  this.subDomain_ = options['subDomain'];
  this.shards_ = options['shards'];
  this.ignoreTypes_ = !!options['ignoreTypes'];
  this.type_ = this.ignoreTypes_ ? null : options['type'];
  this.version_ = options['version'];

  this.copyrights_ = new H.service.MapTileCopyrights();
};
H.lang.inherits(H.service.metaInfo.Service, H.util.EventTarget);


/**
 * @return {number}
 */
H.service.metaInfo.Service.hash = function() {
  var options = H.service.getOptions('metainfotile'),
      str = '';

  str += options['ignoreTypes'] ? '' : options['type'];
  str += options['path'];
  str += options['subDomain'];
  str += options['baseUrl'];
  str += options['shards'] ? options['shards'].join('') : '';

  return H.service.util.hash(str);
};


/**
 * Transforms an info list from the map tile service into a JS-style object
 * @param {Object} infoData
 * @param {string} what
 */
H.service.metaInfo.Service.listToObject = function(infoData, what) {
  'use strict';
  var result = {},
      list = infoData[what + 's'][what],
      i = list.length;
  while (i--) {
    result[list[i].id] = list[i];
  }
  infoData[what + 's'] = result;
};


/**
 * @override
 * @export
 */
H.service.metaInfo.Service.prototype.configure = function(appId, appCode,
    useHTTPS, useCIT, opt_baseUrl) {
  'use strict';

  this.url_ = (this.baseUrl_ || opt_baseUrl).clone();
  this.subDomain_ && this.url_.addSubDomain(this.subDomain_);
  this.type_ && this.url_.addSubDomain(this.type_);
  this.path_ && this.url_.setPath(this.path_);


  if (this.version_ === 'newest') {
    this.fetchInfo();
  }
  return this;
};


/**
 * This method returns the configured service URL.
 * @return {H.service.Url}
 */
H.service.metaInfo.Service.prototype.getUrl = function() {
  return this.url_;
};


/**
 * This method returns the sharding sub domains for this service.
 * @return {(Array.<string>|undefined)}
 */
H.service.metaInfo.Service.prototype.getShards = function() {
  return this.shards_;
};


/**
 * This method returns the map tile type provided by this service.
 * @return {string} the map tile type
 */
H.service.metaInfo.Service.prototype.getType = function() {
  'use strict';
  return this.type_;
};


/**
 * This method returns the mmeta info tile service's newest version hash.
 * @publish
 * @export
 * @return {string} meta information for this map tile service
 */
H.service.metaInfo.Service.prototype.getVersion = function() {
  'use strict';
  return this.version_;
};


/**
 * This method sets this meta tile service's version string
 * @private
 * @param {string} version
 */
H.service.metaInfo.Service.prototype.setVersion_ = function(version) {
  'use strict';
  this.version_ = version;
};


/**
 * Returns this MetaInfoTileService's copyrights collection.
 * @export
 * @return {H.service.MapTileCopyrights}
 */
H.service.metaInfo.Service.prototype.getCopyrights = function() {
  'use strict';
  return this.copyrights_;
};


/**
 * This mathod updates this map tile service's meta information.
 * @param {H.service.ServiceResult} info
 */
H.service.metaInfo.Service.prototype.updateInfo = function(info) {
  'use strict';
  var response = info.response,
      key,
      lang,
      listToObject = H.service.metaInfo.Service.listToObject;

  listToObject(response, 'map');
  listToObject(response, 'scheme');
  listToObject(response, 'tiletype');
  listToObject(response, 'format');
  listToObject(response, 'resolution');
  listToObject(response, 'language');

  if (this.version_ === 'newest' && this.type_ !== 'traffic') {
    for (key in response['maps']) {
      if (response['maps'][key]['hash'] && response['maps'][key]['newest']) {
        this.setVersion_(response['maps'][key]['hash']);

        this.dispatchEvent('versionupdate');
      }
    }
  }

  this.info_ = response;
  this.dispatchEvent('infoupdate');
};


/**
 * This method retrieves meta-information from the map tile backend
 */
H.service.metaInfo.Service.prototype.fetchInfo = function() {
  'use strict';
  var that = this,
      versionUrl = this.getUrl()
        .clone()
        .addSubPath('info')
        .mergeQuery({'output': 'json'});

  //if we have shards, then we pick the first one
  this.shards_ && versionUrl.addSubDomain(this.shards_[0]);

  H.service.jsonp({
    url: versionUrl,
    callbackKey: 'callback_func',
    callback: function(data) {
      //FIX THE JSON-P RESPONSE
      //Maptiler currently sends extraneous commas at the end of arrays
      that.updateInfo(/** @type {Object} */(JSON.parse(data.replace(/,\s?\]/g, ']'))));
    },
    errback: function(failure) {
      throw new Error('[' + failure + ']: Could not fetch meta-info from map tile service @' + versionUrl.toString());
    },
    preventCallbackEncoding: true
  });
};


/**
 * This method returns the meta info tile service's meta information. The method will
 * return an object once the map tile service's data has been fetched.
 * @publish
 * @export
 * @return {H.service.metaInfo.Service.Info|undefined} meta information for this meta info tile service
 */
H.service.metaInfo.Service.prototype.getInfo = function() {
  'use strict';
  return this.info_;
};


/**
 * This method creates a tile provider which uses the meta info tile backend. This provider can be used as a data source
 * for an TileLayer.
 * @publish
 * @export
 * @param {number} tileSize The tile size
 * @param {number} pixelratio
 * @param {Array.<string>=} opt_categoryFilter A list of meta-info category names which should be suppressed. See
 *      [Metainfo Tile](http://developer.here.com/rest-apis/documentation/enterprise-map-tile/topics/tile-metainfo.html)
 *      for valid category names.
 * @param {H.service.ServiceParameters=} opt_additionalParameters Additional parameters for the meta info service
 * @return {H.map.provider.TileProvider} the tile provider
 */
H.service.metaInfo.Service.prototype.createTileProvider = function(tileSize, pixelratio, opt_categoryFilter,
    opt_additionalParameters) {
  'use strict';
  return new H.service.metaInfo.TileProvider(this, opt_additionalParameters,
      {'tileSize': tileSize, 'pixelratio': pixelratio, 'categoryFilter': opt_categoryFilter});
};


/**
 * This method creates a tile layer. This layer can be used as a layer on a map's data model.
 * @publish
 * @export
 * @param {number} tileSize the tile size
 * @param {number} pixelratio the tile's pixel ratio, should be aligned with base map tile
 * @param {Array.<string>=} opt_categoryFilter A list of meta-info category names which should be suppressed. See
 *      [Metainfo Tile](http://developer.here.com/rest-apis/documentation/enterprise-map-tile/topics/tile-metainfo.html)
 *      for valid category names.
 * @param {H.service.ServiceParameters=} opt_additionalParameters Additional parameters for the meta info service
 * @return {H.map.layer.TileLayer} the tile layer
 */
H.service.metaInfo.Service.prototype.createTileLayer = function(tileSize, pixelratio, opt_categoryFilter,
    opt_additionalParameters) {
  'use strict';
  var provider = this.createTileProvider(tileSize, pixelratio, opt_categoryFilter, opt_additionalParameters);
  return new H.map.layer.TileLayer(provider);
};


/**
 * This event is fired when the service's info resource was successfully
 * retrieved and processed.
 * @publish
 * @event H.service.metaInfo.Service#infoupdate
 * @type {H.util.Event}
 */


/**
 * This event is fired when the service's version hash was successfully
 * updated.
 * @publish
 * @event H.service.metaInfo.Service#versionupdate
 * @type {H.util.Event}
 */


/**
 * This event is fired when the service's copyrights were successfully
 * retrieved.
 * @publish
 * @event H.service.metaInfo.Service#copyrightupdate
 * @type {H.util.Event}
 */


/**
 * @publish
 * @property {Object.<string, Object>} maps
 * @property {Object.<string, Object>} schemes
 * @property {Object.<string, Object>} tiletypes
 * @property {Object.<string, Object>} formats
 * @property {Object.<string, Object>} resolutions
 * @property {Object.<string, Object>} languages
 *
 * @typedef {{
 *   maps: Object.<string, Object>,
 *   schemes: Object.<string, Object>,
 *   tiletypes: Object.<string, Object>,
 *   formats: Object.<string, Object>,
 *   resolutions: Object.<string, Object>,
 *   languages: Object.<string, Object>
 * }}
 */
H.service.metaInfo.Service.Info;


/**
 * This method returns a map tile service stub.
 * @export
 * @publish
 * @param {H.service.metaInfo.Service.Options=} opt_params additional service parameters
 * @return {H.service.metaInfo.Service}
 */
H.service.Platform.prototype.getMetaInfoTileService = function(opt_params) {
  'use strict';
  return /** @type {H.service.metaInfo.Service} */ (this.configure(
      new H.service.metaInfo.Service(opt_params)));
};


//Storing default options
H.service.setOptions('metainfotile', {
  'type': 'base',
  'version': 'newest',
  'subDomain': 'maps',
  'path': 'maptile/2.1',
  'shards': ['1', '2', '3', '4']
});


/**
 * @publish
 * @property {string=} version the map version hash to use for retrieving tiles,
 *  default is newest and will be automatically updated
 * @property {string=} subDomain the sub-domain of the map tile service relative
 *  to the platform's base URL, default is {@code 'maps'}
 *
 * @typedef {{
 *   version: (string|undefined),
 *   subDomain: (string|undefined)
 * }}
 */
H.service.metaInfo.Service.Options;
