if (COMPILED) {
  goog.provide('H');
}


/** @suppress {duplicate} */
var H = H || {};


/**
 * This namespace contains classes and interfaces related to the [HERE REST
 * APIs](http://developer.here.com/rest-apis).
 *
 * In particular this namespace contains implementations of service stubs and
 * helper functionality to integrate the HERE Maps for JavaScript API
 * with the following backend services:
 *
 * - [HERE Map Tile API](http://developer.here.com/rest-apis/documentation/enterprise-map-tile)
 * - [HERE Geododer API](http://developer.here.com/rest-apis/documentation/geocoder)
 * - [HERE Routing API](http://developer.here.com/rest-apis/documentation/routing)
 * - [HERE Enterprise Routing API](http://developer.here.com/rest-apis/documentation/enterprise-routing)
 * - [HERE Traffic API](http://developer.here.com/rest-apis/documentation/traffic)
 * - [HERE Places API](http://developer.here.com/rest-apis/documentation/places)
 *
 * @namespace H.service
 * @publish
 */
goog.provide('H.service');

goog.require('H.service.DefaultLayers');
goog.require('H.service.EnterpriseRoutingService');
goog.require('H.service.GeocodingService');
goog.require('H.service.JsonX');
goog.require('H.service.MapTileService');
goog.require('H.service.RoutingService');
goog.require('H.service.PlacesService');
goog.require('H.service.Platform');
goog.require('H.service.metaInfo');


/**
 * Function returns build information
 * @return {H.util.BuildInfo}
 * @export
 */
H.service.buildInfo = function() {
  'use strict';
  return H.util.BuildInfo.get('{BUILD_NAME}', '{BUILD_VERSION}', '{BUILD_REVISION}');
};
