goog.provide('H.service.MapTileCopyrights');



/**
 * This class represents the copyrights for a map tile service. A tile
 * provider can query for the copyrights in a given map view (zoom level and
 *   bounding rectangle) for the correct attribution string.
 *
 * @constructor
 */
H.service.MapTileCopyrights = function() {
  'use strict';
  this.types_ = {
    'hybrid' : this.satellite_,
    'satellite' : this.satellite_,
    'terrain' : this.normal_,
    'normal' : this.normal_
  };
};


/**
 * This method allows to query the copyright string for a given map type,
 * zoom level and bounding rectangle. Copyright information is concatenated
 * and returned as a string.
 * @param {string} type the map type
 * @param {number} level the zoom level
 * @param {H.geo.Rect} rect the bounding rectangle of the area for which to
 *  retrieve the attribution string
 * @return {Array.<Object>}
 */
H.service.MapTileCopyrights.prototype.query = function(type, level, rect) {
  'use strict';
  var copyrights = this.types_[type],
      i,
      l = copyrights ? copyrights.length : 0,
      list = [];
  for (i = 0; i < l; i++) {
    if (copyrights[i].intersects(level, rect)) {
      list.push(copyrights[i]);
    }
  }
  return list;
};



/**
 * This class represents an internal helper class to query copyrights easily.
 * @param {Object} def a copyright definition as received by the map tile service
 * @constructor
 * @private
 */
H.service.MapTileCopyright_ = function(def) {
  'use strict';
  var i, l,
      boxes = def['boxes'],
      box,
      rect;

  this.alt = def['alt'];
  this.label = def['label'];
  this.min = def['minLevel'];
  this.max = def['maxLevel'];
  this.rects = [];

  if (boxes && (l = boxes.length)) {
    box = boxes[0];
    rect = new H.geo.Rect(box[0], box[1], box[2], box[3]);

    this.rects.push(rect);
    this.outer = new H.geo.Rect(box[0], box[1], box[2], box[3]);

    for (i = 1; i < l; i++) {
      box = boxes[i];
      rect = new H.geo.Rect(box[0], box[1], box[2], box[3]);
      this.rects.push(rect);
      this.outer.mergeRect(rect, false, this.outer);
    }
  }
};


/**
 * This method checks if a copyright is valid for the given level and bounding
 * rectangle
 * @param {number} level the zoom level
 * @param {H.geo.Rect} rect the rectangular bounding area
 * @return {boolean} true if this copyright is active, false otherwise
 */
H.service.MapTileCopyright_.prototype.intersects = function(level, rect) {
  'use strict';
  var i;
  if (level >= this.min && level <= this.max) {
    //if we are in the correct zoom level...
    if ((i = this.rects.length) && this.outer) {
      //AND we do have bounding rectangles
      if (this.outer.intersects(rect)) {
        //AND the rectangle intersects the outer bounding rect
        //we need to iterate over the individual rectangles
        while (i--) {
          if (this.rects[i].intersects(rect)) {
            return true;
          }
        }
      }
    } else {
      //if we do not have any rects the copyright is valid everywhere
      return true;
    }
  }
  return false;
};


