goog.provide('H.service.Platform');


goog.require('H.service.Url');



/**
 * The Platform class represents central class from which all other service
 * stubs are created. It also contains the shared settings to be passed to
 * the individual service stubs, for example the root URL of the platform,
 * application credentials, etc.
 *
 * @example
 * var platform = new H.service.Platform({
 *   app_id: [your app_id],       //set your app_id
 *   app_code: [your app_code],   //set your app_code
 *   useCIT: true                 //configure all services to use the 'CIT' environment
 * });
 *
 * var geocoder = plaform.createGeocodingService();
 * //Now you are ready to talk to the HERE Geocoder API
 *
 * @constructor
 * @param {H.service.Platform.Options} options
 * @publish
 * @export
 */
H.service.Platform = function(options) {
  'use strict';
  var options_ = H.service.mergeOptions(H.service.defaultOptions_, options);

  this.setBaseUrl(options_['baseUrl']);
  this.setUseCIT(options_['useCIT']);
  this.setUseHTTPS(options_['useHTTPS']);
  this.setCredentials_(options_['app_id'], options_['app_code']);

  /**
   * Dictionary of tile services that were already created.
   * @private
   * @dict
   */
  this.tileServices_ = {};
};


/**
 * This method sets the credentials to use for authenticating the client
 *  with the platform
 * @param {string} app_id the app id of the client application
 * @param {string} app_code the app code of the client application
 * @private
 */
H.service.Platform.prototype.setCredentials_ = function(app_id, app_code) {
  'use strict';
  this.app_id_ = app_id;
  this.app_code_ = app_code;

  this.baseUrl_.mergeQuery({
    'app_id': app_id,
    'app_code': app_code
  });
};


/**
 * Method attempts to configure object that implements {@code H.service.IConfigurable}
 * @param {H.service.IConfigurable} configurable
 * @return {H.service.IConfigurable}
 * @export
 * @publish
 */
H.service.Platform.prototype.configure = function(configurable) {
  'use strict';
  if (configurable['configure']) {
    return configurable['configure'](this.app_id_, this.app_code_,
        this.useHTTPS_, this.useCIT_, this.getBaseUrl());
  }
  throw new Error('Object is not configurable');
};


/**
 * This method enables or disables HTTPS communication with the platform
 * @param {boolean} useHTTPS a boolean value indicating whether to communicate
 *  with the platform via HTTPS
 * @publish
 * @export
 */
H.service.Platform.prototype.setUseHTTPS = function(useHTTPS) {
  'use strict';
  this.useHTTPS_ = useHTTPS;
  this.baseUrl_.setScheme(useHTTPS ? 'https' : 'http');
};


/**
 * The host of the baseUrl, needed for setUseCIT
 * @private
 * @type {string}
 */
H.service.Platform.prototype.baseHost_;


/**
 * This method configures whether to use the "customer integration testing"
 * instance of the platform.
 * @param {boolean} useCIT a boolean value indicating whether the CIT platform
 *  instance is to be used
 * @publish
 * @export
 */
H.service.Platform.prototype.setUseCIT = function(useCIT) {
  'use strict';
  this.useCIT_ = useCIT;
  this.baseUrl_.setHost((useCIT ? 'cit.' : '') + this.baseHost_);
};


/**
 * This method modifies the base URL to be used when creating service stubs.
 * @param {H.service.Url} baseUrl the new base URL to use
 * @publish
 * @export
 */
H.service.Platform.prototype.setBaseUrl = function(baseUrl) {
  'use strict';
  this.baseHost_ = baseUrl.getHost();
  this.baseUrl_ = baseUrl = baseUrl.clone();
  baseUrl.mergeQuery({ 'xnlp': 'CL_JSMv3.' + H.buildInfo()['version']});
  this.setUseCIT(this.useCIT_);
  this.setUseHTTPS(this.useHTTPS_);
  this.setCredentials_(this.app_id_, this.app_code_);
};


/**
 * This method returns the currently used base URL.
 * @publish
 * @export
 * @return {H.service.Url}
 */
H.service.Platform.prototype.getBaseUrl = function() {
  'use strict';
  return this.baseUrl_;
};


/**
 * @publish
 * @property {string} app_id
 *           The application ID to identify the client against the platform (mandatory to provide)
 * @property {string} app_code
 *           The application code to identify the client against the platform (mandatory to provide)
 * @property {H.service.Url=} baseUrl
 *           The base URL of the platform, default is {@code http://api.here.com}
 * @property {boolean=} useCIT
 *           Indicates whether the Customer Integration Testing should be used, default is {@code false}
 * @property {boolean=} useHTTPS
 *           Indicates whether secure communication should be used, default is {@code false}
 *
 * @typedef {{
 *   app_id: ?string,
 *   app_code: ?string,
 *   useHTTPS: boolean,
 *   useCIT: boolean,
 *   baseUrl: H.service.Url
 * }}
 */
H.service.Platform.Options;


/**
 * This property holds the global default options
 * @dict
 * @private
 */
H.service.defaultOptions_ = {
  'app_id': null,
  'app_code': null,

  'useHTTPS': false,
  'useCIT': false,
  'baseUrl': new H.service.Url('http', 'api.here.com')
};


/**
 * @export
 * @param {string} key the key which identifies the options to set
 * @param {Object} value the configuration values to set
 */
H.service.setOptions = function(key, value) {
  H.service.defaultOptions_[key] = value;
};


/**
 * @export
 * @param {string} key the key which identifies the options to set
 * @return {Object} returns the configuration values
 */
H.service.getOptions = function(key) {
  return H.service.defaultOptions_[key];
};


/**
 * Merges an options overriding the provided default options.
 * @param {Object} defaultOptions the default values
 * @param {Object=} opt_options the overriding option values
 * @return {Object}
 */
H.service.mergeOptions = function(defaultOptions, opt_options) {
  'use strict';
  var options = {},
      key;
  for (key in defaultOptions) {
    if (defaultOptions.hasOwnProperty(key) && !(key in options)) {
      options[key] = defaultOptions[key];
    }
    if (opt_options && opt_options.hasOwnProperty(key)) {
      options[key] = opt_options[key];
    }
  }

  return options;
};


/**
 * This type encapsulates URL parameters to be sent to a HERE platform service.
 *
 * When calling HERE REST APIs additional URL parameters can be sent by providing
 * an object parameter which contain URL parameters in key-value format, where
 * keys are URL parameter names and the values are the parameter values.
 *
 * Note: The object assumes that values are in string format.
 *
 * @example
 * serviceParameters = {
 *   paramA: 'valueA',
 *   paramB: 'valueB'
 * }
 *
 * @typedef {Object}
 * @publish
 */
H.service.ServiceParameters;


/**
 * This type encapsulates a response object provider by a HERE platform service.
 *
 * In most cases the HERE Maps API for JavaScript will communicate to the
 * HERE REST APIs via the JSON-P protocol. In such cases the resulting JSON
 * response is parsed and provided to the callback function.
 *
 * The structure of a service response object is specific to each service.
 *
 * @typedef {Object}
 * @publish
 */
H.service.ServiceResult;
