goog.provide('H.service.MapTileService');


goog.require('H.service.IConfigurable');
goog.require('H.service.MapTileProvider');
goog.require('H.service.Platform');
goog.require('H.service.jsonp');
goog.require('H.service.util');



/**
 * This class encapsulates a map tile end point of the
 * [HERE Map Tile API](http://developer.here.com/rest-apis/documentation/enterprise-map-tile).
 *
 * An instance of this class can be retrieved by calling the factory method on
 * a platform instance. {@link H.service.Platform#getMapTileService}.
 *
 * @example
 * var platform = new H.service.Platform(),
 *     maptiler = platform.getMapTileService({type: 'aerial'}),
 *     tileLayer = maptiler.createTileLayer('maptile', 'hybrid.day', 256, 'jpg');
 * map.setBaseLayer(tileLayer);
 *
 * @extends {H.util.EventTarget}
 * @implements {H.service.IConfigurable}
 * @constructor
 * @param {H.service.MapTileService.Options=} opt_options
 * @publish
 * @export
 */
H.service.MapTileService = function(opt_options) {
  'use strict';
  var options = H.service.mergeOptions(H.service.getOptions('maptile'), opt_options);

  H.util.EventTarget.call(this);

  this.baseUrl_ = options['baseUrl'];
  this.path_ = options['path'];
  this.subDomain_ = options['subDomain'];
  this.shards_ = options['shards'];
  this.ignoreTypes_ = !!options['ignoreTypes'];
  this.type_ = this.ignoreTypes_ ? null : options['type'];
  this.version_ = options['version'];

  this.copyrights_ = new H.service.MapTileCopyrights();

  /**
   * Stores all providers created by this map tile service
   * @private
   * @dict
   */
  this.providers_ = {};
};
H.lang.inherits(H.service.MapTileService, H.util.EventTarget);


/**
 * @param {H.service.MapTileService.Options=} opt_options
 * @return {number}
 */
H.service.MapTileService.hash = function(opt_options) {
  var options = H.service.mergeOptions(H.service.getOptions('maptile'),
      opt_options),
      str = '';

  str += options['ignoreTypes'] ? '' : options['type'];
  str += options['path'];
  str += options['subDomain'];
  str += options['baseUrl'];
  str += options['shards'] ? options['shards'].join('') : '';

  return H.service.util.hash(str);
};


/**
 * Transforms an info list from the map tile service into a JS-style object
 * @param {Object} infoData
 * @param {string} what
 */
H.service.MapTileService.listToObject = function(infoData, what) {
  'use strict';
  var result = {},
      list = infoData[what + 's'][what],
      i = list.length;
  while (i--) {
    result[list[i].id] = list[i];
  }
  infoData[what + 's'] = result;
};


/**
 * @override
 * @export
 */
H.service.MapTileService.prototype.configure = function(appId, appCode,
    useHTTPS, useCIT, opt_baseUrl) {
  'use strict';

  this.url_ = (this.baseUrl_ || opt_baseUrl).clone();
  this.subDomain_ && this.url_.addSubDomain(this.subDomain_);
  this.type_ && this.url_.addSubDomain(this.type_);
  this.path_ && this.url_.setPath(this.path_);


  if (this.version_ === 'newest') {
    this.fetchInfo();
  }
  return this;
};


/**
 * This method returns the configured service URL.
 * @return {H.service.Url}
 */
H.service.MapTileService.prototype.getUrl = function() {
  return this.url_;
};


/**
 * This method returns the sharding sub domains for this service.
 * @return {(Array.<string>|undefined)}
 */
H.service.MapTileService.prototype.getShards = function() {
  return this.shards_;
};


/**
 * This method returns the map tile type provided by this service.
 * @return {string} the map tile type
 * @publish
 * @export
 */
H.service.MapTileService.prototype.getType = function() {
  'use strict';
  return this.type_;
};


/**
 * This method returns the map tile service's newest version hash.
 * @return {string} meta information for this map tile service
 * @publish
 * @export
 */
H.service.MapTileService.prototype.getVersion = function() {
  'use strict';
  return this.version_;
};


/**
 * This method sets this map tile service's version string
 * @param {string} version
 * @private
 */
H.service.MapTileService.prototype.setVersion_ = function(version) {
  'use strict';
  this.version_ = version;
};


/**
 * Returns this MapTileService's copyrights collection.
 * @return {H.service.MapTileCopyrights}
 * @export
 */
H.service.MapTileService.prototype.getCopyrights = function() {
  'use strict';
  return this.copyrights_;
};


/**
 * This mathod updates this map tile service's meta information.
 * @param {H.service.ServiceResult} info
 */
H.service.MapTileService.prototype.updateInfo = function(info) {
  'use strict';
  var response = info.response,
      key,
      lang,
      listToObject = H.service.MapTileService.listToObject;

  listToObject(response, 'map');
  listToObject(response, 'scheme');
  listToObject(response, 'tiletype');
  listToObject(response, 'format');
  listToObject(response, 'resolution');
  listToObject(response, 'language');

  if (this.version_ === 'newest' && this.type_ !== 'traffic') {
    for (key in response['maps']) {
      if (response['maps'][key]['hash'] && response['maps'][key]['newest']) {
        this.setVersion_(response['maps'][key]['hash']);

        this.dispatchEvent('versionupdate');
      }
    }
  }

  this.info_ = response;
  this.dispatchEvent('infoupdate');
};


/**
 * This method retrieves meta-information from the map tile backend
 */
H.service.MapTileService.prototype.fetchInfo = function() {
  'use strict';
  var that = this,
      versionUrl = this.getUrl()
        .clone()
        .addSubPath('info')
        .mergeQuery({'output': 'json'});

  //if we have shards, then we pick the first one
  this.shards_ && versionUrl.addSubDomain(this.shards_[0]);

  H.service.jsonp({
    url: versionUrl,
    callbackKey: 'callback_func',
    callback: function(data) {
      //FIX THE JSON-P RESPONSE
      //Maptiler currently sends extraneous commas at the end of arrays
      that.updateInfo(/** @type {Object} */(JSON.parse(data.replace(/,\s?\]/g, ']'))));
    },
    errback: function(failure) {
      throw new Error('[' + failure + ']: Could not fetch meta-info from map tile service @' + versionUrl.toString());
    },
    preventCallbackEncoding: true
  });
};


/**
 * This method returns the map tile service's meta information. The method will
 * return an object once the map tile service's data has been fetched.
 * @return {H.service.MapTileService.Info|undefined} meta information for this map tile service
 * @publish
 * @export
 */
H.service.MapTileService.prototype.getInfo = function() {
  'use strict';
  return this.info_;
};


/**
 * This method creates a tile provider which uses the specified map tiles. This provider can be used as a data source
 * for an ImageTileLayer.
 *
 * @param {string} tileType the tile type
 * @param {string} scheme the tile scheme
 * @param {number} tileSize the tile size
 * @param {string} format the tile image format
 * @param {H.service.ServiceParameters=} opt_additionalParameters a hash of additional parameters to be
 * @return {H.map.provider.ImageTileProvider} the image tile provider
 * @publish
 * @export
 */
H.service.MapTileService.prototype.createTileProvider = function(tileType, scheme, tileSize, format,
    opt_additionalParameters) {
  'use strict';
  var hash = H.service.MapTileProvider.hash(this, tileType, scheme, tileSize, format, opt_additionalParameters),
      provider = this.providers_[hash];
  if (!provider) {
    provider = new H.service.MapTileProvider(this, tileType, scheme, tileSize, format, opt_additionalParameters);
    this.providers_[hash] = provider;
  }

  return provider;
};


/**
 * This method creates a tile layer. This layer can be used as a layer on a map's data model.
 * @param {string} tileType the tile type
 * @param {string} scheme the tile scheme
 * @param {number} tileSize the tile size
 * @param {string} format the tile image format
 * @param {H.service.ServiceParameters=} opt_additionalParameters Additional parameters for the map tile service
 * @param {number=} opt_opacity The opacity of this layer
 * @param {boolean=} opt_dark Indicates whether the content of this layer is mainly dark, default is {@code false}
 *           See also {@link H.Map.Options#autoColor}
 * @return {H.map.layer.TileLayer} the tile layer
 * @publish
 * @export
 */
H.service.MapTileService.prototype.createTileLayer = function(tileType, scheme, tileSize, format,
    opt_additionalParameters, opt_opacity, opt_dark) {
  'use strict';
  var provider = this.createTileProvider(tileType, scheme, tileSize, format, opt_additionalParameters);
  return new H.map.layer.TileLayer(provider, {
    'opacity': opt_opacity,
    'dark': !!opt_dark
  });
};


/**
 * This event is fired when the service's info resource was successfully
 * retrieved and processed.
 * @event H.service.MapTileService#infoupdate
 * @type {H.util.Event}
 * @publish
 */


/**
 * This event is fired when the service's version hash was successfully
 * updated.
 * @event H.service.MapTileService#versionupdate
 * @type {H.util.Event}
 * @publish
 */


/**
 * This event is fired when the service's copyrights were successfully
 * retrieved.
 * @event H.service.MapTileService#copyrightupdate
 * @type {H.util.Event}
 * @publish
 */


/**
 * @typedef {{
 *   type: string,
 *   version: (string|undefined),
 *   subDomain: (string|undefined),
 *   path: (string|undefined)
 * }}
 * @property {string=} type the type of the map tile service to communicate
 *  with, e.g. 'base' (default), 'aerial', etc. (refer to the Map Tile REST API
 *  documentation for available types)
 * @property {string=} version the map version hash to use for retrieving tiles,
 *  default is newest and will be automatically updated
 * @property {string=} subDomain the sub-domain of the map tile service relative
 *  to the platform's base URL, default is {@code 'maps'}
 * @property {string=} path the path of the map tile service, default is
 *  {@code 'maptile/2.1'}
 * @publish
 */
H.service.MapTileService.Options;


//Storing default options
H.service.setOptions('maptile', {
  'type': 'base',
  'version': 'newest',
  'subDomain': 'maps',
  'path': 'maptile/2.1',
  'shards': ['1', '2', '3', '4']
});


/**
 * @typedef {{
 *   maps: Object.<string, Object>,
 *   schemes: Object.<string, Object>,
 *   tiletypes: Object.<string, Object>,
 *   formats: Object.<string, Object>,
 *   resolutions: Object.<string, Object>,
 *   languages: Object.<string, Object>
 * }}
 * @property {Object.<string, Object>} maps
 * @property {Object.<string, Object>} schemes
 * @property {Object.<string, Object>} tiletypes
 * @property {Object.<string, Object>} formats
 * @property {Object.<string, Object>} resolutions
 * @property {Object.<string, Object>} languages
 * @publish
 */
H.service.MapTileService.Info;


/**
 * This method returns a map tile service stub.
 * @param {H.service.MapTileService.Options=} opt_options
 * @return {H.service.MapTileService}
 * @export
 * @publish
 */
H.service.Platform.prototype.getMapTileService = function(opt_options) {
  'use strict';
  var hash = H.service.MapTileService.hash(opt_options),
      mapTileService = this.tileServices_[hash];
  if (!mapTileService) {
    mapTileService = this.configure(new H.service.MapTileService(opt_options));
    this.tileServices_[hash] = mapTileService;
  }
  return /** @type {H.service.MapTileService} */ (mapTileService);
};
