goog.provide('H.service.RoutingService');


goog.require('H.service.AbstractRestService');
goog.require('H.service.Platform');



/**
 * This class encapsulates the Routing REST API as a service stub.
 * An instance of this class can be retrieved by calling the factory method on
 * a platform instance. {@link H.service.Platform#getRoutingService}.
 *
 * @example
 * var platform = new H.service.Platform(),
 *     router = platform.getRoutingService();
 *
 * var calculateRouteParams = {
 *       'waypoint0': 'geo!52.5,13.4',
 *       'waypoint1': 'geo!52.5,13.45',
 *       'mode': 'fastest;car;traffic:disabled'
 *     },
 *     onResult = function(result) {
 *       console.log(result.response.route[0]);
 *     },
 *     onError = function(error) {
 *        console.log(error);
 *     };
 * router.calculateRoute(calculateRouteParams, onResult, onError);
 *
 * @extends {H.service.AbstractRestService}
 * @constructor
 * @param {H.service.RoutingService.Options=} opt_options
 * @publish
 * @export
 */
H.service.RoutingService = function(opt_options) {
  'use strict';
  var options = H.service.mergeOptions(H.service.getOptions('routing'), opt_options);
  options['callbackKey'] = 'jsoncallback';

  H.util.getSuperProto(H.service.RoutingService).constructor.call(this, options);
};
H.lang.inherits(H.service.RoutingService, H.service.AbstractRestService);


/**
 * This method sends a "calculateroute" request to Routing REST API and
 * calls the {@code onResult} callback function once the service response
 * was received - providing a {@link H.service.ServiceResult} object -
 * or the {@code onError} callback if a communication error occured.
 *
 * Please refer to the
 * [Routing REST API documentation](http://developer.here.com/rest-apis/documentation/routing)
 * for information on available parameters and the response object structure.
 *
 * @param {H.service.ServiceParameters} calculateRouteParams the service
 *  parameters to be sent with the routing request.
 * @param {function(H.service.ServiceResult)} onResult this function
 *  will be called once the
 *  [Routing REST API](http://developer.here.com/rest-apis/documentation/routing)
 *  provides a response to the request.
 * @param {function(Error)} onError this function will
 *  be called if a communication error occurs during the JSON-P request
 * @publish
 * @export
 */
H.service.RoutingService.prototype.calculateRoute = function(calculateRouteParams, onResult, onError) {
  'use strict';
  var url = this.getUrl()
      .clone()
      .addSubPath('calculateroute.json')
      .mergeQuery(calculateRouteParams);
  this.restApiRequest(url, onResult, onError);
};


//Storing default options
H.service.setOptions('routing', {
  'subDomain': 'route',
  'path': 'routing/7.2'
});


/**
 * @typedef {{
 *    subDomain: (string|undefined),
 *    path: (string|undefined),
 *    baseUrl: (H.service.Url|undefined)
 * }}
 * @property {string=} subDomain the sub-domain of the routing service relative
 *  to the platform's base URL, default is {@code 'route'}
 * @property {string=} path the path of the map tile service, default is
 *  {@code 'routing/7.2'}
 * @property {H.service.Url=} baseUrl an optional base URL if it differs
 *  from the platform's default base URL
 * @publish
 */
H.service.RoutingService.Options;


/**
 * This method returns an instance of {@link H.service.RoutingService}.
 * @param {H.service.RoutingService.Options=} opt_options
 * @return {H.service.RoutingService}
 * @publish
 * @export
 */
H.service.Platform.prototype.getRoutingService = function(opt_options) {
  'use strict';
  return /** @type {H.service.RoutingService} */ (this.configure(
      new H.service.RoutingService(opt_options)));
};
