goog.provide('H.service.TrafficIncidentsIcons');



/**
 * This class provides marker icons for traffic incidents.
 * @constructor
 */
H.service.TrafficIncidentsIcons = function() {
  /**
   * @private
   * @dict
   */
  this.icons_ = {};
};


/**
 * Returns a marker icon for a given type of incident and a givent severity
 * @param {string} type one of 'CONGESTION', 'ROADWORKS', 'ACCIDENT' or 'OTHER
 * @param {string} severity one of 'VERYHIGH', 'HIGH' or 'BLOCKING'
 * @return {H.map.Icon}
 */
H.service.TrafficIncidentsIcons.prototype.getIcon = function(type, severity) {
  var key = type + severity,
      iconMarkup,
      color,
      icon,
      iconOptions;
  if (!(icon = this.icons_[key])) {
    iconOptions = {
      'size': { 'w': 26, 'h': 32 },
      'anchor': { 'x': 13, 'y': 30 }
    };

    if (type !== 'CLOSURE') {
      iconMarkup = H.service.TrafficIncidentsIcons.MARKER_;
      switch (type) {
        case 'CONGESTION':
          iconMarkup = iconMarkup.replace('{{icon}}', H.service.TrafficIncidentsIcons.CONGESTION_);
          break;
        case 'ROADWORKS':
          iconMarkup = iconMarkup.replace('{{icon}}', H.service.TrafficIncidentsIcons.ROADWORKS_);
          break;
        case 'ACCIDENT':
          iconMarkup = iconMarkup.replace('{{icon}}', H.service.TrafficIncidentsIcons.ACCIDENT_);
          break;
        default:
          iconMarkup = iconMarkup.replace('{{icon}}', H.service.TrafficIncidentsIcons.OTHER_);
          break;
      }
    } else {
      iconMarkup = H.service.TrafficIncidentsIcons.CLOSURE_;
      iconOptions['anchor']['y'] = 16;
    }

    color = H.service.TrafficIncidentsIcons.colors_[severity] ||
            H.service.TrafficIncidentsIcons.colors_['BLOCKING'];
    iconMarkup = iconMarkup.replace('{{color}}', color);

    this.icons_[key] = icon = new H.map.Icon(iconMarkup,
        /** @type {H.map.Icon.Options} */ (iconOptions));
  }
  return icon;
};


/**
 * @private
 * @dict
 */
H.service.TrafficIncidentsIcons.colors_ = {
  // listed in order of severity
  'BLOCKING': '#323232',
  'VERYHIGH': '#d5232f',
  'HIGH': '#ffa100'
};


