goog.provide('H.service.EnterpriseRoutingService');


goog.require('H.service.Platform');
goog.require('H.service.jsonp');



/**
 * This class encapsulates Enterprise Routing REST API as a service stub.
 * An instance of this class can be retrieved by calling the factory method on
 * a platform instance. {@link H.service.Platform#getEnterpriseRoutingService}.
 *
 * @example
 * var platform = new H.service.Platform(),
 *     router = platform.getEnterpriseRoutingService();
 *
 * var calculateIsoline = {
 *       'start': 'geo!52.5,13.4',
 *       'distance': '1000,2000',
 *       'mode': 'fastest;car;traffic:disabled'
 *     },
 *     onResult = function(result) {
 *       console.log(result.response.isolines[0]);
 *     },
 *     onError = function(error) {
 *        console.log(error);
 *     };
 * router.calculateIsoline(calculateIsoline, onResult, onError);
 *
 * @extends {H.service.AbstractRestService}
 * @constructor
 * @param {H.service.EnterpriseRoutingService.Options=} opt_options
 * @publish
 * @export
 */
H.service.EnterpriseRoutingService = function(opt_options) {
  'use strict';
  var options = H.service.mergeOptions(H.service.getOptions('enterpriseRouting'), opt_options);
  options['callbackKey'] = 'jsoncallback';

  H.util.getSuperProto(H.service.EnterpriseRoutingService).constructor.call(this, options);
};
H.lang.inherits(H.service.EnterpriseRoutingService, H.service.AbstractRestService);


/**
 * This method sends a "calculateroute" request to Enterprise Routing REST API and
 * calls the {@code onResult} callback function once the service response
 * was received - providing a {@link H.service.ServiceResult} object -
 * or the {@code onError} callback if a communication error occured.
 *
 * Please refer to the
 * [Routing REST API documentation](http://developer.here.com/rest-apis/documentation/enterprise-routing)
 * for information on available parameters and the response object structure.
 *
 * @export
 * @publish
 * @param {H.service.ServiceParameters} calculateRouteParams the service
 *  parameters to be sent with the routing request.
 * @param {function(H.service.ServiceResult)} onResult this function
 *  will be called once the
 *  [Enterprise Routing REST API](http://developer.here.com/rest-apis/documentation/enterprise-routing)
 *  provides a response to the request.
 * @param {function(Error)} onError this function will
 *  be called if a communication error occurs during the JSON-P request
 */
H.service.EnterpriseRoutingService.prototype.calculateRoute = function(calculateRouteParams,
    onResult, onError) {
  'use strict';

  var url = this.getUrl()
      .clone()
      .addSubPath('calculateroute.json')
      .mergeQuery(calculateRouteParams);
  this.restApiRequest(url, onResult, onError);
};


