/**
 * @publish
 * @property {string=} subDomain
 *      The sub-domain of the routing service relative to the platform's base URL (default is {@code 'route'})
 * @property {string=} path
 *      The path of the map tile service, default is {@code "routing/7.2"}
 * @property {H.service.Url=} baseUrl
 *      The base URL of the service, defaults to the the platform's base URL

 * @typedef {{
 *    subDomain: (string|undefined),
 *    path: (string|undefined),
 *    baseUrl: (H.service.Url|undefined)
 * }}
 */
H.service.EnterpriseRoutingService.Options;


//Storing default options
H.service.setOptions('enterpriseRouting', {
  'subDomain': 'route',
  'path': 'routing/6.2',
  'baseUrl': new H.service.Url('http', 'nlp.nokia.com')
});


/**
 * This method returns a service stub to query the
 * [Enterprise Routing REST API](http://developer.here.com/rest-apis/documentation/enterprise-routing).
 * @param {H.service.EnterpriseRoutingService.Options=} opt_options
 * @return {H.service.EnterpriseRoutingService}
 * @publish
 * @export
 */
H.service.Platform.prototype.getEnterpriseRoutingService = function(opt_options) {
  'use strict';
  return /** @type {H.service.EnterpriseRoutingService} */ (
      this.configure(new H.service.EnterpriseRoutingService(opt_options)));
};
