goog.provide('H.service.metaInfo.IObject');



/**
 * This interface represents additional properties for map objects from remote object providers
 * @interface
 */
H.service.metaInfo.IObject = function() {};


/**
 * Returns this object's remote ID which is used to identify different remote objects who are represented as one and
 * the same map object.
 * @export
 * @return {string}
 */
H.service.metaInfo.IObject.prototype.getRemoteId = function() {};


/**
 * A method which is called whenever a new reference to this object is retained.
 * @export
 */
H.service.metaInfo.IObject.prototype.retain = function() {};


/**
 * A method which is called whenever a previously retained reference to this object is now released.
 * A concrete implementation of this method has to call H.service.metaInfo.ObjectProvider#removeObject
 * when all references to this object are released.
 * @export
 * @return {boolean} to indicate if all references to this object are released
 */
H.service.metaInfo.IObject.prototype.release = function() {};
