goog.provide('H.service.metaInfo.Parser');

goog.require('H.service.metaInfo.IParser');
goog.require('H.service.metaInfo.Spatial');



/**
 * A concrete parser implementation for
 * [Metainfo Tiles](http://developer.here.com/rest-apis/documentation/enterprise-map-tile/topics/tile-metainfo.html).
 *
 * The parser provides aditional features like:
 *
 *  * Cleanups of meaningless meta-info properties as well as geometry data to reduce the memory foot print.
 *  * Adds a "category" property to each meta-info.
 *  * Stores the meta-info for all created map objects {@see H.map.Object#getData}.
 *  * Creates hybrid spatial objects for buildings (see H.map.Spatial.Options#hybridIdx).
 *  * Determines objects with the same remote ID and utilize the remote object managment of the provider.
 *
 * @implements {H.service.metaInfo.IParser}
 * @constructor
 * @param {Array.<string>=} opt_categoryFilter A list of meta-info category names which should be suppressed.
 *     See section
 *     [Metainfo Tile](http://developer.here.com/rest-apis/documentation/enterprise-map-tile/topics/tile-metainfo.html)
 *     for valid category names.
 */
H.service.metaInfo.Parser = function(opt_categoryFilter) {
  'use strict';
  var i;

  if (opt_categoryFilter) {
    this.suppressedCategories_ = {};
    i = opt_categoryFilter.length;
    while (i--) {
      this.suppressedCategories_[opt_categoryFilter[i]] = true;
    }
  }

  // Initialize the map for reusable "street label" styles:
  this.streetLabelStyles_ = {};
};


/**
 * A mapping of category names and a their suppression state
 * We set a same-named instance property if any category is suppressed
 * @private
 * @type {Object.<string, boolean>}
 */
H.service.metaInfo.Parser.prototype.suppressedCategories_ = {};


/**
 * A flag to indicate whether the parsing failed
 * @private
 * @type {boolean}
 */
H.service.metaInfo.Parser.parseFailed_;


/**
 * A helper method to signalize a failed parse
 * @private
 */
H.service.metaInfo.Parser.faile_ = function() {
  H.service.metaInfo.Parser.parseFailed_ = true;
};


/**
 * @override
 * @export
 */
H.service.metaInfo.Parser.prototype.parse = function(provider, tile, response) {
  'use strict';
  var categories,
      category,
      entityParser,
      entities,
      i,
      entity,
      parserResult,
      UNDEF;

  // Reset the parseFailed-flag:
  H.service.metaInfo.Parser.parseFailed_ = false;

  // Handle potential exceptions which may occur during JSON parsing:
  try {
    response = H.service.util.global.JSON.parse(response);
  } catch (ex) {
    H.service.metaInfo.Parser.faile_();
  }
  if (response && (categories = response['metadata'])) {
    parserResult = /** @type {H.service.metaInfo.IParser.Result} */ ({'spatials': []});
    // Iterate over all categories:
    for (category in categories) {
      // Skip categories which are filtered out or for which no entityParser exists:
      if (!this.suppressedCategories_[category] &&
          (entityParser = H.service.metaInfo.Parser.entityParsers_[category])) {
        if (entities = categories[category] || H.service.metaInfo.Parser.faile_()) {
          // Iterate over all entities within the category:
          i = entities.length;
          while (i--) {
            if (entity = entities[i] || H.service.metaInfo.Parser.faile_()) {
              entityParser(tile, provider, this, category, entity, parserResult);
            }
          }
        }
      }
    }
  } else {
    H.service.metaInfo.Parser.faile_();
  }
  return H.service.metaInfo.Parser.parseFailed_ ? UNDEF : parserResult;

};


//###############################################  ENTITY PARSING  #####################################################
/**
 * A dictionary of entity parsers for all known meta-info categories.
 * The expected arguments are:
 *
 *  * the according spatial tile of the response
 *  * the objects provider to use for map object creation
 *  * the parser instance
 *  * the category
 *  * the response's entity to parse
 *  * the parser result set to store the resulting objects
 *
 * @private
 * @type {Object.<string, function(
 *     H.map.provider.SpatialTile,
 *     H.service.metaInfo.ObjectProvider,
 *     H.service.metaInfo.Parser,
 *     string,
 *     Object,
 *     H.service.metaInfo.IParser.Result
 * )>}
 */
H.service.metaInfo.Parser.entityParsers_ = {


  //------------------------------------------------  STREET LABELS  ---------------------------------------------------
  'street labels': function(tile, provider, parser, category, entity, result) {
    'use strict';
    var verteces;
    if (verteces = entity['vertices'] || H.service.metaInfo.Parser.faile_()) {
      H.service.metaInfo.Parser.createSpatial_(
          tile,
          provider,
          H.service.metaInfo.Parser.verticesToGeometry_(verteces),
          false,
          parser.getStreetLabelStyle_(tile, entity['font size']),
          entity,
          category,
          result,
          -1);
    }
    // Pre-cleanup the meta-info data to reduce cleanup effort:
    delete entity['vertices'];
  },


  //---------------------------------------------  CITY CENTER LABELS  -------------------------------------------------
  'city center labels': function(tile, provider, parser, category, entity, result) {
    'use strict';
    H.service.metaInfo.Parser.createSpatial_(
        tile,
        provider,
        H.service.metaInfo.Parser.boundingBoxMapToGeometry_(entity, []),
        true,
        parser.defaultStyle_,
        entity,
        category,
        result,
        -1);
  },


  //---------------------------------------------------  LABELS  -------------------------------------------------------
  'labels': function(tile, provider, parser, category, entity, result) {
    'use strict';
    var boundingBoxes;
    if (boundingBoxes = entity['bounding boxes'] || H.service.metaInfo.Parser.faile_()) {
      H.service.metaInfo.Parser.createSpatial_(
          tile,
          provider,
          H.service.metaInfo.Parser.boundingBoxListToGeometry_(boundingBoxes),
          true,
          parser.defaultStyle_,
          entity,
          category,
          result,
          -1);
    }
    // Pre-cleanup the meta-info data to reduce cleanup effort:
    delete entity['bounding boxes'];
  },


  //-------------------------------------------------  BUILDINGS  ------------------------------------------------------
  'buildings': function(tile, provider, parser, category, entity, result) {
    'use strict';
    // A variant of the buildings entity parser which takes all sectors into account:
    var remoteId = entity['id'],
        spatial = provider.fetchObjectById(remoteId),
        sectors,
        i,
        length,
        sector,
        vertices,
        indeces,
        polygons = [],
        outlines = [],
        geometry,
        hybridIdx;

    if (sectors = entity['sectors'] || H.service.metaInfo.Parser.faile_()) {
      length = sectors.length;
      // Iterate over all sectors:
      for (i = 0; i < length; i++) {
        if (sector = sectors[i] || H.service.metaInfo.Parser.faile_()) {
          if (vertices = sector['vertices']) {
            if (indeces = sector['polygons']) {
              polygons.push.apply(polygons, H.service.metaInfo.Parser.vertexIndicesToGeometry_(vertices, indeces));
            }
            if (indeces = sector['outlines']) {
              outlines.push.apply(outlines, H.service.metaInfo.Parser.vertexIndicesToGeometry_(vertices, indeces));
            }
          }
          // Pre-cleanup the meta-info data to reduce cleanup effort:
          delete sector['vertices'];
          delete sector['polygons'];
          delete sector['outlines'];
        }
      }
    }

    hybridIdx = polygons.length;
    geometry = polygons;
    geometry.push.apply(geometry, outlines);

    if (geometry.length || H.service.metaInfo.Parser.faile_()) {
      // An object with the same remote ID could be fetched from then provider?
      if (spatial) {
        // Just add the geometry for the given tile and store the spatial within the parser result:
        spatial.setGeometry(tile, geometry, hybridIdx);
        result['spatials'].push(spatial);
      } else {
        // Create a new spatial object, push it to the result list and add it to the remote provider
        spatial = H.service.metaInfo.Parser.createSpatial_(
            tile,
            provider,
            geometry,
            true,
            parser.defaultStyle_,
            entity,
            category,
            result,
            hybridIdx,
            remoteId);
        provider.addObject(spatial);
      }
    }
  }

};

//-------------------------------------------  TRANSIT_STOPS + POIs  ---------------------------------------------------
// The "transit_stops" and "POIs" parsers are identical with "labels" parser:
H.service.metaInfo.Parser.entityParsers_['transit_stops'] =
    H.service.metaInfo.Parser.entityParsers_['POIs'] =
        H.service.metaInfo.Parser.entityParsers_['labels'];


//#############################################  GEOMETRY PARSING  #####################################################
/**
 * To convert meta-info vertices into a geometry
 * @private
 * @param {Array.<number>} vertices A list vertices as alternating x and y coordinates
 * @return {Array.<Array.<H.math.Point>>} the resulting geometry
 */
H.service.metaInfo.Parser.verticesToGeometry_ = function(vertices) {
  'use strict';
  var i = vertices.length,
      geometry,
      x,
      y;
  if (i > 0 || H.service.metaInfo.Parser.faile_()) {
    geometry = new Array(i / 2);
    while (i) {
      y = vertices[--i];
      x = vertices[--i];
      geometry[i / 2] = new H.math.Point(x, y);
    }
  }
  return geometry ? [geometry] : [];
};


/**
 * To convert a meta-info box into a sub-geometry.
 * @private
 * @param {Array.<number>} box A meta data box, formatted as [left, top, width, height]
 * @return {Array.<H.math.Point>} the resulting sub-geometry. Be aware that it returns an array of
 *                                     H.math.Point but NOT an array of arrays of H.math.Point.
 */
H.service.metaInfo.Parser.boxToGeometry_ = function(box) {
  'use strict';
  var x = box[0],
      y = box[1],
      w = box[2],
      h = box[3];
  return [
    new H.math.Point(x, y),          // top left
    new H.math.Point(x + w, y),      // top right
    new H.math.Point(x + w, y + h),  // bottom right
    new H.math.Point(x, y + h)       // bottom left
  ];
};


/**
 * To convert a list of "bounding boxes" into a geometry
 * @private
 * @param {Array.<Object>} boundingBoxList A list of bounding box maps
 * @return {Array.<Array.<H.math.Point>>} the resulting geometry
 */
H.service.metaInfo.Parser.boundingBoxListToGeometry_ = function(boundingBoxList) {
  'use strict';
  var i = boundingBoxList.length,
      geometry = [],
      boundingBoxMap;
  while (i--) {
    if (boundingBoxMap = boundingBoxList[i] || H.service.metaInfo.Parser.faile_()) {
      H.service.metaInfo.Parser.boundingBoxMapToGeometry_(boundingBoxMap, geometry);
    }
  }
  return geometry;
};


/**
 * A list of all supported names of bounding boxes
 * @private
 * @type {Array.<string>}
 */
H.service.metaInfo.Parser.boundingBoxKeys_ = ['text box', 'bullet box', 'annotation box'];


/**
 * Parses a map of bounding boxes like
 *   {
 *     "text box":       [x, y, w, ,h],
 *     "bullet box":     [x, y, w, ,h],
 *     "annotation box": [x, y, w, ,h]
 *   }
 * and push the resulting sub-geometries to the given geometry
 * @private
 * @param {Object} boundingBoxMap A map of bounding boxes
 * @param {Array.<Array.<H.math.Point>>} geometry
 * @return {Array.<Array.<H.math.Point>>} the geometry
 */
H.service.metaInfo.Parser.boundingBoxMapToGeometry_ = function(boundingBoxMap, geometry) {
  'use strict';
  var i = H.service.metaInfo.Parser.boundingBoxKeys_.length,
      key,
      box;
  while (i--) {
    key = H.service.metaInfo.Parser.boundingBoxKeys_[i];
    if (box = boundingBoxMap[key]) {
      geometry.push(H.service.metaInfo.Parser.boxToGeometry_(box));
      // Pre-cleanup the meta-info data to reduce cleanup effort:
      delete boundingBoxMap[key];
    }
  }
  return geometry;
};


/**
 * To convert meta-info vertex-indices into a geometry
 * @private
 * @param {Array.<number>} vertices A list of vertices as alternating x and y values
 * @param {Array.<Array.<number>>} indices A list of vertex indices to discribe the geometry
 * @return {Array.<Array.<H.math.Point>>} the resulting geometry
 */
H.service.metaInfo.Parser.vertexIndicesToGeometry_ = function(vertices, indices) {
  'use strict';
  var i = indices.length,
      subIndices,
      geometry,
      subGeometry,
      j,
      idx;

  // EXAMPLE:
  // vertices = [1, 2,   4, 2,   4, 6,   1, 6];                           // A rectangular box
  // indices = [[3, 1, 2], [1, 3, 2]];                                    // tessellated into 2 triangles
  // geometry = [[(1, 6), (4, 2), (1, 2)], [(4, 2), (1, 6), (4, 6)]];     // the resulting geometry
  if (i > 0) {
    geometry = Array(i);
    while (i--) {
      if (subIndices = indices[i] || H.service.metaInfo.Parser.faile_()) {
        j = subIndices.length;
        if (j > 0) {
          geometry[i] = subGeometry = Array(j);
          while (j--) {
            idx = subIndices[j] * 2;
            subGeometry[j] = new H.math.Point(vertices[idx], vertices[idx + 1]);
          }
        }
      }
    }
  }
  return geometry || [];
};


//##############################################  SPATIAL CREATION  ####################################################
/**
 * A reusable object to use as spatial options
 * @private
 * @type {H.map.Spatial.Options}
 */
H.service.metaInfo.Parser.spatialOptions_ = /** @type {H.map.Spatial.Options} */ ({});


/**
 * A helper function to create and prepare a spatial objects
 * @private
 * @param {H.map.provider.SpatialTile} tile
 * @param {H.service.metaInfo.ObjectProvider} provider
 * @param {Array.<Array.<H.math.Point>>} geometry
 * @param {boolean} isClosed
 * @param {H.map.SpatialStyle} style
 * @param {Object} data
 * @param {string} category
 * @param {H.service.metaInfo.IParser.Result} result
 * @param {number} hybridIdx
 * @param {string=} opt_remoteId
 * @return {H.service.metaInfo.Spatial}
 */
H.service.metaInfo.Parser.createSpatial_ = function(
    tile, provider, geometry, isClosed, style, data, category, result, hybridIdx, opt_remoteId) {
  'use strict';
  var spatial;

  H.service.metaInfo.Parser.spatialOptions_['style'] = style;
  spatial = new H.service.metaInfo.Spatial(
      provider, isClosed, opt_remoteId, H.service.metaInfo.Parser.spatialOptions_);

  spatial.setGeometry(tile, geometry, hybridIdx);
  // Set the meta-info data:
  H.service.metaInfo.Parser.cleanupMetaInfoData_(0, data);
  data['category'] = category;
  spatial.setData(data);

  result['spatials'].push(spatial);

  spatial.setParentEventTarget(provider);
  return spatial;
};


//###############################################  SPATIAL STYLES  #####################################################
/**
 * @private
 * @type {string}
 */
H.service.metaInfo.Parser.TRANSPARENT_ = 'transparent';


/**
 * The spatial style to use by default.
 * It has full transparent fill- and stroke-color to be not rendered but detectable in hit test
 * @private
 * @type {H.map.SpatialStyle}
 */
H.service.metaInfo.Parser.prototype.defaultStyle_ = new H.map.SpatialStyle({
  strokeColor: H.service.metaInfo.Parser.TRANSPARENT_,
  fillColor: H.service.metaInfo.Parser.TRANSPARENT_,
  //strokeColor: 'rgba(0,0,0,.3)',
  //fillColor: 'rgba(255,0,0,.2)',
  lineWidth: 0
});


/**
 * Mapping of "font-size" to an SpatialStyle with corresponding lineWidth
 * @private
 * @type {Object.<*, H.map.SpatialStyle>}
 */
H.service.metaInfo.Parser.prototype.streetLabelStyles_;


/**
 * Reusable object to pass style attributes to SpatialStyle#getCopy
 * @private
 * @type {H.map.SpatialStyle.Options}
 */
H.service.metaInfo.Parser.streetLabelStyleAttributes_ = /** @type {H.map.SpatialStyle.Options} */ ({});


/**
 * Returns a "street label" style according to the given font size
 * @private
 * @param {H.map.provider.SpatialTile} tile
 * @param {string} fontSize
 * @return {H.map.SpatialStyle}
 */
H.service.metaInfo.Parser.prototype.getStreetLabelStyle_ = function(tile, fontSize) {
  'use strict';
  var style,
      lineWidth = +fontSize / tile.getPixelRatio();
  style = this.streetLabelStyles_[lineWidth];
  if (!style) {
    H.service.metaInfo.Parser.streetLabelStyleAttributes_['lineWidth'] = lineWidth;
    style = this.streetLabelStyles_[lineWidth] = this.defaultStyle_.getCopy(
        H.service.metaInfo.Parser.streetLabelStyleAttributes_);
  }
  return style;
};


//############################################  META-INFO DATA CLEANUP  ################################################
/**
 * Cleans up meta-info data by removing all meaningless fields recursively
 * We treat as meaningless: empty strings, false, null, empty arrays and empty objects.
 * Caused of revcursive calls an object like { "foo": [], "bar": {}, "foobar": "" } will be cleaned to { } and
 * the return value is false because it's an empty object (after cleanup).
 * An object like { "foo": [], "bar": {}, "foobar": 4711 } will be cleaned to { "foobar": 4711 } and
 * the return value is true because it's not empty.
 * @private
 * @param {string|number} key The property name or index of the data value
 * @param {*} value The meta-info data value to clean up
 * @return {boolean} Indicates wheter the data value is meaningfull
 */
H.service.metaInfo.Parser.cleanupMetaInfoData_ = function(key, value) {
  'use strict';
  var i,
      isMeaningfull = false;

  switch (typeof value) {
    case 'object': {
      if (value instanceof Array) {
        i = value.length;
        while (i--) {
          // Recursion!:
          if (H.service.metaInfo.Parser.cleanupMetaInfoData_(i, value[i])) {
            isMeaningfull = true;
          } else {
            delete value[i];
          }
        }
      } else {
        // It's an instance of Object or null
        if (value) {
          // It's an instance of Object
          for (i in value) {
            // Recursion:
            if (H.service.metaInfo.Parser.cleanupMetaInfoData_(i, value[i])) {
              isMeaningfull = true;
            } else {
              delete value[i];
            }
          }
        }
      }
      break;
    }
    case 'number': {
      isMeaningfull = true;
      break;
    }
    default: {
      isMeaningfull = !!value;
    }
  }

  return isMeaningfull;
};
